<template>
    <section>
        <loader :is-visible="isLoading"></loader>        

        <tg-list :showCheckBox="true" :IsShowAction="true" :listType="['List']"
               ModuleName="CompanySetup" SubModuleCode="ManageTax" :PageSize="PageSize"
               IdentityColumn="tax_id" :HeaderText="$t('ManageTax')"
               :widgets="widgets" :ListData="TaxList" :HeaderData="Header"
               :callbackfunction="FetchData" :LegendArray="LegendArray"
               :SearchFields="leftSearchFields" :SortExp="SortExp" :SortBy="SortBy"
               :NorecordfoundText="$t('NorecordfoundText')" :TotalRecords="TotalRecord"
               :HeaderButtons="HeaderButtons"
               :RenderRowActionMethod="renderActions" :ListDataCallBackFunction="FetchData"
               v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
               v-on:SortdataButtonClick="sortdata" v-on:ActionButtonClick="actionButtonClick">
               <template v-slot:slotdata="{ data }">                                       
                    <template v-if="data.column.COLUMN_NAME == 'tax_name'">
                        <a href="javascript:;" v-on:click="OpenTaxPopUp(data.row)">
                            <i aria-hidden="true"></i>
                           {{ data.row.tax_name }}
                        </a>                        
                    </template>
               </template>
           </tg-list>       
        <AddItemPopup v-if="AddItem" :TaxData="ItemData" @fetch-data="FetchData"></AddItemPopup>        
    </section>
</template>
<script>
import DataService from '../../../services/DataService';
 import AddItemPopup from './AddItemCodeModel.vue'
export default {
    components: { AddItemPopup },
    
    data() {
        return {
            isLoading: false,
            TaxList: [],
            ItemData:[],
            leftSearchFields: '',
            listheaderbuttons: [],
            LegendArray: [],
            TotalRecord:5,
            PageSize: 10,
            statusId: '',
            PageNumber: 1,
            isPaged: 1,
            TotalPages: 0,
            widgets: [],
            SortBy: '',
            SortExp: '',
            AddItem:false,
            Header: [
                        {
                            "COLUMN_NAME": "tax_name",
                            "DATA_TYPE": "text",
                            "SORTABLE": true,
                            "VISIBLE": true,
                            "DISPLAY_NAME": this.$t("TaxName"),
                            "DISPLAY_ORDER": 1,                           
                            settings: {
                                isInSlot: true,
                            }                   
                        },
                        {
                            "COLUMN_NAME": "GST1",
                            "DATA_TYPE": "text",
                            "SORTABLE": true,
                            "VISIBLE": true,
                            "DISPLAY_NAME":  this.$t("GSTCode"),
                            "DISPLAY_ORDER": 2,
                        },
                        {
                            "COLUMN_NAME": "COMMENT",
                            "DATA_TYPE": "text",
                            "SORTABLE": false,
                            "VISIBLE": true,
                            "DISPLAY_NAME":  this.$t("DESCRIPTION"),
                            "DISPLAY_ORDER": 3,
                        },
                        {
                            "COLUMN_NAME": "tax_for",
                            "DATA_TYPE": "text",
                            "SORTABLE": true,
                            "VISIBLE": true,
                            "DISPLAY_NAME": this.$t("TaxFor"),
                            "DISPLAY_ORDER": 4,
                        },
                        {
                            "COLUMN_NAME": "country_name",
                            "DATA_TYPE": "text",
                            "SORTABLE": true,
                            "VISIBLE": true,
                            "DISPLAY_NAME":  this.$t("TaxCountry"),
                            "DISPLAY_ORDER": 5,
                        },
                        {
                            "COLUMN_NAME": "tax_state",
                            "DATA_TYPE": "text",
                            "SORTABLE": true,
                            "VISIBLE": true,
                            "DISPLAY_NAME":  this.$t("TaxState"),
                            "DISPLAY_ORDER": 6,
                        },
                        {
                            "COLUMN_NAME": "tax_district",
                            "DATA_TYPE": "text",
                            "SORTABLE": true,
                            "VISIBLE": true,
                            "DISPLAY_NAME":  this.$t("TaxDistrict"),
                            "DISPLAY_ORDER": 7,
                        },
                        {
                            "COLUMN_NAME": "tax_zipcode",
                            "DATA_TYPE": "text",
                            "SORTABLE": true,
                            "VISIBLE": true,
                            "DISPLAY_NAME":  this.$t("TaxZipcode"),
                            "DISPLAY_ORDER": 8,
                        },
                        {
                            "COLUMN_NAME": "tax_type",
                            "DATA_TYPE": "text",
                            "SORTABLE": true,
                            "VISIBLE": true,
                            "DISPLAY_NAME":  this.$t("TaxType"),
                            "DISPLAY_ORDER": 9,
                        },
                        {
                            "COLUMN_NAME": "tax_rate",
                            "DATA_TYPE": "text",
                            "SORTABLE": true,
                            "VISIBLE": true,
                            "DISPLAY_NAME":  this.$t("TaxRate"),
                            "DISPLAY_ORDER": 10,
                        },
                        {
                            "COLUMN_NAME": "status_id",
                            "DATA_TYPE": "text",
                            "SORTABLE": true,
                            "VISIBLE": true,
                            "DISPLAY_NAME":  this.$t("StatusId"),
                            "DISPLAY_ORDER": 11,
                        },
                        {
                            "COLUMN_NAME": "created_by",
                            "DATA_TYPE": "text",
                            "SORTABLE": true,
                            "VISIBLE": true,
                            "DISPLAY_NAME":  this.$t("CreatedBy"),
                            "DISPLAY_ORDER": 12,
                        },
                        {
                            "COLUMN_NAME": "created_at",
                            "DATA_TYPE": "text",
                            "SORTABLE": true,
                            "VISIBLE": true,
                            "DISPLAY_NAME":  this.$t("CreatedAt"),
                            "DISPLAY_ORDER": 13,
                        },
                        {
                            "COLUMN_NAME": "modified_by",
                            "DATA_TYPE": "text",
                            "SORTABLE": true,
                            "VISIBLE": true,
                            "DISPLAY_NAME":  this.$t("ModifiedBy"),
                            "DISPLAY_ORDER": 14,
                        },
                        {
                            "COLUMN_NAME": "modified_at",
                            "DATA_TYPE": "text",
                            "SORTABLE": true,
                            "VISIBLE": true,
                            "DISPLAY_NAME":  this.$t("ModifiedAt"),
                            "DISPLAY_ORDER": 15,
                        },                 
            ],
            HeaderButtons :
            [
                {
                    title: 'Add',
                    iconClass: 'fa fa-plus',
                    callbackfunction: 'AddTax',
                    additionalClass: '',
                    isdisabled: false,
                    isvisible: true,
                    checkPrivilege: true,
                    id: 'AddTax',
                },
                {
                    title: 'Remove',
                    iconClass: 'fa-trash-o',
                    callbackfunction: 'commonDelete',
                    additionalClass: '',
                    isdisabled: true,
                    isvisible: true,
                    checkPrivilege: false,
                    id: 'DeleteMultiple',
                    isEnabledOnSelectedRow: true
                }
            ],            
            listActions:[ 
                    {
                        title: 'Edit',
                        href: 'javascript:void(0)',
                        callbackfunction: 'EditTax',
                        icon: 'fa fa-pencil text-success action-icon',
                        additionalSpan: ''
                    },  
                    {
                        title: 'Delete',
                        href: 'javascript:void(0)',
                        icon: 'fa fa-trash',
                        callbackfunction: 'DeleteTax',
                        additionalSpan: ''
                    },
                    {
                        title: 'Share',
                        callbackfunction: 'sharereport',
                        href: 'javascript:void(0)',
                        icon: 'fa fa-share-alt text-success action-icon',
                        additionalSpan: ''
                    },
            ],            
        }
    },
    created: async function () {
        var vm = this;
        vm.user_id = this.GetUserInfo.ID;
        vm.companyId = this.GetUserInfo.CompanyId;
        vm.FetchData();       
    },
    methods: {
        FetchData() {
            var vm = this;
            vm.isLoading = true;
           // var url = `Id=${vm.GetUserAssignBonus.user_id}`;
             DataService.GetTaxList().then(function (response) {                
                vm.isLoading = false;
                if (response.data != '' && response.data != null) {
                    if (response.data.length > 0) {                       
                        vm.TaxList = JSON.parse(response.data);
                    }
                }setTimeout(function () { vm.ResponsiveDataTable('tablelistingdata') }, 500);
            })
        },
        AddTax:function(){
            var vm=this;
            vm.AddItem=true;
            vm.ItemData=[];
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "AddTax":
                    this.AddTax();
                    break;
                case "commonDelete":
                    this.DeleteTax();
                    break;
            }
        },
        DeleteTax: function (event) {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Tax")), function () {
                    var postJSON = {
                        ids: val,
                        moduleName: "CompanySetup",
                        refCode: "talygen_tax"
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataService.CommonCommonDelete(objectPostString).then(response => {
                        if (response.status == 200) {                           
                            vm.FetchData();
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Tax')]), "Success", true, vm.$t('Alert'));
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Tax')]), "failure", true, vm.$t('Alert'));
                        }
                    });
                });
            }
            else {
                vm.ShowAlert(vm.$t('Selectanyrecordtodelete', [vm.$t('Tax')]), "failure", true, vm.$t('Alert'));
            }
        },
        renderActions: function (row) {            
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    case 'Delete':
                        action.isVisible = true;
                        break;
                    case 'Share':
                        action.isVisible = false;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        }, 
        actionButtonClick: function (callbackfunction, item, index, event) {
            var vm = this;      
            switch (callbackfunction) {
                case 'EditTax':
                    vm.OpenTaxPopUp(item);
                    break;
                case 'DeleteTax':
                    vm.Delete(item);
                    break;                
            }
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.FetchData();
        }, 
        OpenTaxPopUp(item) {
            var vm = this;
            vm.AddItem=true,
            vm.ItemData=item            
        },     
    }
}
</script>
