<template>
    <div>
        <loader :is-visible="isLoading"></loader>
        <div class="row">
         <div class="col-12">
                <LeftColumn  v-if="isActive"/>   
                <div class="right-content"> 
                    <div class="tab-content">
                        <div class="expand-list">
                            <a href="javascript:;" class="expand-list-btn justify-content-center align-items-center">
                                <i class="icon-solid arrow-solid-left text-white" style="font-size:9pt"></i>
                            </a>
                        </div>                       
                        <!-- Conditional rendering of components based on subModuleCode -->
                        <CheckList v-if="subModuleCode === 'CheckList'"></CheckList>
                        <Tax v-else-if="subModuleCode === 'ManageTax'"></Tax>
                        <TrainingFeedback v-else-if="subModuleCode === 'ManageFeedback'"></TrainingFeedback>
                        <Discount v-else-if="subModuleCode === 'ManageDiscount'"></Discount>
                        <RightContent 
                        v-else 
                        :moduleCode="moduleCode" 
                        :subModuleCode="subModuleCode" 
                        :primaryKeyforModule="primaryKeyforModule" 
                        :addAPIUrl="addAPIUrl" 
                        :ActionColunName="ActionColunName" 
                        :addUserGuide="addUserGuide">
                        </RightContent>    
                        <!-- Display the subModuleCode for debugging -->
                        
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import LeftColumn from "../Company/LeftColumn.vue";
    import RightContent from "../Company/RightContent.vue";
    import CheckList from '../Company/CheckList/CkeckListCommon.vue'
    import Tax from '../Company/Tax/index.vue'
    import Discount from '../Company/Discounts/index.vue'
     import TrainingFeedback from '../Company/TrainingFeedback/List.vue'
    export default {
        components: {
            LeftColumn,
            RightContent,
            CheckList,
            Tax,
            Discount,
            TrainingFeedback
        },
        props:{
            moduleCode: {
                type: String,
                default: ""
            },
            subModuleCode: {
                type: String,
                default: ""
            },
            primaryKeyforModule:{
                type: String,
                default: ""
            },
            addAPIUrl:{
                type: String,
                required: true
            },
            ActionColunName:{
                type: String,
                required: true
            },
            addUserGuide:{
                type: String,
                required: true
            },           

        }, 
        data() {
            return {
                isActive: true, 
                isLoading:false
            };
        },
        created(){  
            const urlParams = new URLSearchParams(window.location.search);
            const reqfrom = urlParams.get('reqFrom')?urlParams.get('reqFrom'):urlParams.get('reqhFrom');

            if(reqfrom=='14'){
                this.isActive= false;
                setTimeout(() => {                   
                    $('.right-content').addClass('right-content-expand');
                }, 200);               
            }

        }
    }
</script>