<template>
    <section>
            <loader :is-visible="isLoading"></loader>
            <tg-list ref="tgList" :listType="['List']" :IsShowAction="true" :showCheckBox="true" ModuleName="Company"
                SubModuleCode="CheckList" IdentityColumn="checklist_id" :HeaderText="$t('CheckList')" :isShowHeaderText="true"
                :ListData="ListingData" :HeaderData="Headers" :widgets="[]" :HeaderButtons="Buttons"
                :TotalRecords="TotalRecords" :RenderRowActionMethod="renderActions" v-on:LeftsearchButtonClick="leftCommonSearch"
                :IsSearchFieldsOnTop="false" SearchPosition="top" :SearchFields="topsearchField" v-on:ActionButtonClick="actionButtonClick"
                :ListDataCallBackFunction="FetchCheckListing" :SortExp="SortExp" :SortBy="SortBy" :LegendArray="[]"
                :NorecordfoundText="$t('NoRecordfound')" v-on:PagerButtonClick="pagerMethod"
                v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata"> 
                <template v-slot:slotdata="{ data }">
                    <template v-if="data.column.COLUMN_NAME == 'status_id'">
                        <listing-status :isfor="'CHECKLIST'" ref="updatestatus" v-bind:dataid="data.row.checklist_id"
                            data-controller="CHECKLIST" v-bind:statuslist="StatusListForListing" v-bind:item="data.status_id"
                            data-action="index" v-bind:dataselected="data.row.status_id == 1001 ? 'Active' : 'Inactive'"
                            v-bind:callbackfunction="FetchCheckListing" v-bind:isselectedvaluestring="true">
                        </listing-status>
                    </template>
                    <template v-if="data.column.COLUMN_NAME == 'checklist_name'" >
                        <a style="cursor: pointer;" class="task-dec" @click="EditCheckList(data.row)">{{ data.row.checklist_name }}</a>
                    </template>
                </template>               
            </tg-list>
            <AddCheckList v-if="IsAddCheckList" :CheckListId="CheckListId"></AddCheckList>
            <CloneCheckList v-if="IsCloneCheckList" :CheckListId="CheckListId"></CloneCheckList>
    </section>
</template>
<script>
import DataService from '../../../services/DataService';
import Schema from './CheckListSchema'
import AddCheckList from './AddCheckList.vue'
import CloneCheckList from './CloneCheckList.vue';
export default {
    components:{AddCheckList,CloneCheckList},
    data(){
        return{
            isLoading:false,
            Buttons: Schema.buttons,
            searchCondition:'',
            listActions: Schema.listActions,
            StatusListForListing: [{ STATUS: 'Active', STATUS_ID: 1001, STATUS_COLOR: "#ffffff", STATUS_BG_COLOR: "#28a745" },
            { STATUS: 'Inactive', STATUS_ID: 1002, STATUS_COLOR: "#ffffff", STATUS_BG_COLOR: "#d72435" }],
            Headers:[{
                        "COLUMN_NAME": "checklist_name",
                        "DATA_TYPE": "string",
                        "SORTABLE": true,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "ChecklistName",
                        "DISPLAY_ORDER": 1,
                        settings : {
                                isInSlot: true
                            }
                    },
                    {
                        "COLUMN_NAME": "industry_type_name",
                        "DATA_TYPE": "string",
                        "SORTABLE": true,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "Industry Type",
                        "DISPLAY_ORDER": 2
                    },
                    {
                        "COLUMN_NAME": "module_name",
                        "DATA_TYPE": "string",
                        "SORTABLE": true,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "moduleName",
                        "DISPLAY_ORDER": 2
                    },
                    {
                        "COLUMN_NAME": "status_id",
                        "DATA_TYPE": "string",
                        "SORTABLE": false,
                        "VISIBLE": true,
                        "DISPLAY_NAME": "Status",
                        "DISPLAY_ORDER": 3,
                        settings : {
                                isInSlot: true
                            }
                    }],
            ListingData:[],
            PageSize: 10,
            PageNumber: 1,
            isPaged: 1,
            TotalPages: 0,
            widgets: [],
            SortBy: '',
            SortExp: '',
            TotalRecords:0,
            topsearchField : [             
                {                   
                    fieldType: "text",
                    fieldSearchConditionName: ['TC.checklist_name'],
                    paramName: "TC.checklist_name",
                    isSearch: true,
                    value: "",
                    listOptions: []
                },
            ],
            IsAddCheckList:false,
            CheckListStatus:[],
            CheckListId:0,
            userId: null,
            companyId: null,
            IsCloneCheckList:false
        }
    },
    created:function(){
        this.FetchCheckListing();
        this.userId = this.GetUserInfo.ID;
        this.companyId = this.GetUserInfo.CompanyId;  
    },
    updated(){        
        const divElement = document.getElementsByClassName('ticketsmallfix theme-primary partition p-actions-expand')
        if (divElement[0] && divElement[0].lastChild.className != 'p-name text-white' ) {
            
            const spanElement = document.createElement('span')
            spanElement.className = "p-name text-white"
            spanElement.textContent = this.$t("CheckList");

            divElement[0].appendChild(spanElement);
        }
    },
    methods:{        
        actionButtonClick: function (callbackfunction, item) {
            switch (callbackfunction) {
                case 'commonDeleteData':
                    this.commonDeleteData(item.checklist_id)
                    break;
                case 'CloneCheckList':
                    this.CloneCheckList(item.checklist_id);
                    break;                
            }
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;                   
            if (value.searchConditionJson != '') {
                let sqlCondition  = value.searchConditionJson;                
                let match = sqlCondition.match(/=(.*)/);
                if (match) {
                    this.searchCondition = `TC.checklist_name like '%${match[1]}%'`;
                } else {
                    console.log("No match found");
                }
            } else {
                this.searchCondition = "";
            }
            this.FetchCheckListing();
        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchCheckListing();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchCheckListing();
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Add':
                        action.isVisible =  true;
                        break;
                    case 'Clone Check List':
                        action.isVisible =  true;
                        break;                  
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        commonHeaderButtonClick: function (event) {        
            switch (event.callbackfunction) {
                case "AddCheckList":
                    this.AddCheckList(event);
                    break;  
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;               
            }
        },
        FetchCheckListing(){
            var ths = this;           
            if(ths.searchCondition != ''){
                var url=`search=${ths.searchCondition}&pageSize=${ths.PageSize}&pageNum=${ths.PageNumber}&sortBy=${ths.SortBy}&sortExp=${ths.SortExp}&ModuleName=`
            }else{
                var url=`search=&pageSize=${ths.PageSize}&pageNum=${ths.PageNumber}&sortBy=${ths.SortBy}&sortExp=${ths.SortExp}&ModuleName=`
            }
            ths.isLoading=true;
            DataService.CheckListListing(url).then((response)=>{
                ths.isLoading=false;
                if(response.data.result.Data.length>0){
                    this.ListingData=response.data.result.Data;
                    ths.TotalRecords = response.data.result.Data[0].TotalRecord;
                    ths.TotalPages = Math.ceil(ths.TotalRecords / ths.PageSize);
                    ths.CurrentPage = ths.PageNumber;
                }else{
                    ths.ListingData=[];
                    ths.TotalRecords = 0;
                }
            })
        },
        AddCheckList(){
            this.CheckListId=0;
            this.IsAddCheckList=true;
        },
        EditCheckList(itm){
            this.CheckListId=itm.checklist_id;
            this.IsAddCheckList=true;
        },
        commonDeleteData: function (id) {
            var vm = this;
            var val = "";
            if(id != undefined){
                val=id;
            }else{
                $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                    if (val.length > 0)
                        val += ',';
                    val += $(this).val();
                });
            } 
            vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("CheckList")), function () {
                var postJSON = {
                    ids: val,
                    companyId: vm.companyId,
                    userId: vm.userId,
                    moduleName: "Project",
                    refCode: "CHECKLIST"
                };
                var objectPostString = JSON.stringify(postJSON);
                DataService.CommonCommonDelete(objectPostString).then(response => {
                    if (response.data[0].Status == "Success") {
                        vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('CheckList')]), "Success", true, vm.$t('Alert'));
                        vm.FetchCheckListing();
                    }
                    else {
                        vm.ShowAlert(vm.$t('DeletedError', [vm.$t('CheckList')]), "failure", true, vm.$t('Alert'));
                    }
                });
            });
        },
        CloneCheckList(value){
            var ths=this;
            ths.CheckListId=value;
            ths.IsCloneCheckList=true;
        }
    }
}
</script>