            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'status_name'">
                    <span v-if="data.row.company_id > 0">
                        <a href="javascript:;" v-on:click="clickEventOfColumn(data.row,data.column)"><i aria-hidden="true"></i>{{
                            data.row.status_name }}
                        </a>
                    </span>
                    <span v-else>
                        {{data.row.status_name }}
                    </span>
                 
               </template>
                <template v-if="data.column.COLUMN_NAME == 's'">
                    <template v-if="data.row.s.Name != null">
                        <div>{{ data.row.s.Name }}</div>
                    </template>
                    <template v-else>
                        <div>--</div>
                    </template>                   
                </template>
                <template v-if="data.column.COLUMN_NAME == 'start_time'">
                    <template v-if="data.row.start_time != null">
                        <div>{{ formatTime(data.row.start_time) }}</div>                        
                    </template>
                    <template v-else>
                        <div>--</div>
                    </template>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'end_time'">
                    <template v-if="data.row.end_time != null">
                        <div>{{ formatTime(data.row.end_time) }}</div>                        
                    </template>
                    <template v-else>
                        <div>--</div>
                    </template>
                </template>

                <template v-if="data.column.COLUMN_NAME == 'shift_type'">
                    <template v-if="data.row.shift_type == 1" >
                        <div>Flexible</div>                       
                    </template>
                    <template v-if="data.row.shift_type == 0">
                        <div>Fixed</div>
                    </template>
                </template>

                <template v-if="data.column.COLUMN_NAME == 'break_types'">
                    <template v-if="data.row.break_types == 1" >
                        <div>Paid</div>                       
                    </template>
                    <template v-if="data.row.break_types == 0">
                        <div>UnPaid</div>
                    </template>
                </template>
                 <template v-if="data.column.COLUMN_NAME == 'no_of_times'">
                    <template>
                        <div>{{data.row.no_of_times==null?0:data.row.no_of_times}}</div>                       
                    </template>
                  
                </template>
              

                <template v-if="data.column.COLUMN_NAME == 'description'">
                    <template v-if="data.row.description != null">
