                    </template>
                    <template v-else>
                        <div>--</div>
                    </template>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'from_user_id'">
                    <template v-if="data.row.from_user_id.Name != null">
                        <div>{{ data.row.from_user_id.Name }}</div>
                    </template>
                    <template v-else>
                        <div>--</div>
                    </template>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'to_user_id'">
                    <template v-if="data.row.to_user_id.Name != null">
                        <div>{{ data.row.to_user_id.Name }}</div>
                    </template>
                    <template v-else>
                        <div>--</div>
                    </template>
                </template>

                <template v-if="data.column.COLUMN_NAME == 'is_skill_enable'">
                    <template v-if="data.row.is_skill_enable == 1">
