                        <div class="fa fa-check text-success"></div>
                    </template>
                    <template v-else>
                        <div class="fa fa-check text-secondary"></div>
                    </template>
                </template>
                
                <template v-if="data.column.COLUMN_NAME == 'is_goal_enable'">
