                    <template v-if="data.row.source_currency_id.name != null">
                        <div>{{ data.row.source_currency_id.name }}</div>
                    </template>
                    <template v-else>
                        <div>--</div>
                    </template>
                </template> -->
                
                <!-- {{getStatus(data.row.status_id)}}{{ data.row[primaryKeyforModule] }} --> 
                <template v-if="data.column.COLUMN_NAME == 'status_id'">                              
                    <listing-status :isfor="'COMPANYSETUP_UPDATE'" v-if="isCRMShowStausDrop(data.row.status_id, 'COMPANYSETUP_UPDATE')"
