                        v-bind:statuslist="StatusListForListing" v-bind:item="data.row[primaryKeyforModule]" data-action="index" :module=moduleCode :subModule=subModuleCode
                        v-bind:dataselected="getStatus(data.row.status_id) == '1001' ? 'Active' : 'Inactive'"
                        v-bind:title ="getStatus(data.row.status_id) == '1001' ? 'Active' : 'Inactive'"
                        v-bind:callbackfunction="FetchData" v-bind:isselectedvaluestring="true">
                    </listing-status>
                </template>

                <template v-if="data.column.COLUMN_NAME == 'is_active' && subModuleCode != 'Status'" >                          
