                        v-bind:dataselected="data.row.is_active == 'ACTIVE' ? 'Active' : 'Inactive'"
                        v-bind:title ="data.row.is_active == 'ACTIVE' ? 'Active' : 'Inactive'"
                        v-bind:callbackfunction="FetchData" v-bind:isselectedvaluestring="true">
                    </listing-status>
