    },
    async created() {
        var ths = this;
        ths.CompanyId = ths.GetUserInfo.CompanyId;
        ths.privilegeParams = `controller=Break&action=`;
        await ths.FetchData();
        // Schema.bindleftCommonSearchdropdown(ths, 'Dynamic_Reporting', 'FOLDER_NAME')
        // Schema.bindleftCommonSearchdropdown(ths, 'Dynamic_Reporting', 'CREATED_BY')
        // Schema.bindleftCommonSearchdropdown(ths, 'Dynamic_Reporting', 'MODIFIED_BY')
         ths.getStatuslistForListing("Dynamic_Reporting", "en");
    },
    computed: {
        filteredUserData() {
            return this.vueObj.UserData.map((item) => {
            const filteredItem = { ...item };

            // Check if USER_EMP_ID is null or empty, then replace with '--'
            if (filteredItem.USER_EMP_ID === null || filteredItem.USER_EMP_ID === '') {
                filteredItem.USER_EMP_ID = '--';
            }

            return filteredItem;
            });
        },
        
    },
    mounted() {
        var ths = this;
        ths.topsearchField.forEach(item => {
            item.value = '';
            item.isSearch = false;
        })
    },

    updated(){
        const divElement = document.getElementsByClassName('theme-primary partition p-actions-expand')
        if (divElement[0] && divElement[0].lastChild.className != 'p-name text-white' ) {
            const spanElement = document.createElement('span')
            spanElement.className = "p-name text-white"
            if(this.subModuleCode ==='PerformanceReview'){
                spanElement.textContent =  this.$t('ReviewTemplate');
            }
            else
            {
                spanElement.textContent = this.$t(this.subModuleCode);
            }
            divElement[0].appendChild(spanElement);
        }
    },

    methods: {
        getStatusClassList: function (status) {
            var returnClass = '';
            status = status.toLowerCase();
            switch (status) {
                case 'active':
                    returnClass = 'px-2 text-center text-white d-block bg-success';
                    break;
                case 'inactive':
                    returnClass = 'px-2 text-center text-white d-block bg-danger';
                    break;
                default:
                    returnClass = '';
            }
            return returnClass;
        },
        getStatus: function(obj){
            try {
                const statusIdObj = JSON.parse(obj);
                return statusIdObj.value;
            } 
            catch (error) {
                console.error('Error parsing status_id:', error);
                return obj;
            }
        },

        formatTime(time) {
            if (time) {
                return moment(time).format('hh:mm A');
            } else {
                return '--';
            }
        },

        AddChildLocation: function (id) {
            debugger
            var ths = this;
            ths.moduleCode='Company'
            ths.subModuleCode='Location';
            if (id == undefined) {
                id = 0;
            }
            ths.refId = id;
            ths.showAddPopup = true;
        },
