        commonDeleteData: function (event) {
            var vm = this;
            // CompanyDataService.CheckPrivilege(vm.privilegeParams+'Delete').then((response)=>{
            //     if(response.data){
                    var val = "";
                    $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(function () {
                        if (val.length > 0)
                            val += ',';
                        val += $(this).val();
                    });
                    if (val.length > 0) {
                        if(this.subModuleCode=='Location' && this.moduleCode=='Company')
                        {
                            vm.confirmR(vm.$t("ConfirmDeleteLocation"), true, (vm.$t("Delete") + "  " + vm.$t("Report")), function () {
                            var postJSON = {
                                IDS: val,
                                moduleName: vm.moduleCode,
                                subModuleCode: vm.subModuleCode,
                                refCode: "COMPANYSETUP_DELETE"
                            };
                            var objectPostString = JSON.stringify(postJSON);
                            CompanyDataService.DeleteLocation(objectPostString).then(async function (response) {
                                if (response.data.code == '1') {
                                    $('.chkItems:checkbox:checked,#chkAll_0').prop("checked", false).removeAttr('checked');
                                    $("#DeleteMultiple").addClass("disabled");
                                        vm.ShowAlert(vm.$t('DeletedSuccess',true,[vm.subModuleCode ]), 'success', true, vm.$t('Alert'));
                                    vm.PageNumber = parseInt(vm.PageNumber) - 1;
                                    if (vm.PageNumber <= 0) {
                                        vm.PageNumber = 1;
                                    }
                                    await vm.FetchData();
                                }
                                else {
                                    vm.ShowAlert(vm.$t('DeletedError',true,[vm.subModuleCode ]), 'failure', true, vm.$t('Alert'));
                                   
                                }

                            },);
                        });
                         
                        }else{
