                                    if (response.status == 200) {
                                        $('.chkItems:checkbox:checked,#chkAll_0').prop("checked", false).removeAttr('checked');
                                        $("#DeleteMultiple").addClass("disabled");
                                        // if(vm.moduleCode == "Appointment" && vm.subModuleCode == "Service")
                                        // {
                                            vm.ShowAlert(vm.$t('DeletedSuccess',true,[vm.subModuleCode ]), 'success', true, vm.$t('Alert'));
                                        // }
                                        // else
                                        // {
                                        //     vm.ShowAlert(vm.$t('DeletedSuccessfully', [vm.$t('Records')]), "success", true, vm.$t("Alert"));
                                        // }
                                        
                                        vm.PageNumber = parseInt(vm.PageNumber) - 1;
                                        if (vm.PageNumber <= 0) {
                                            vm.PageNumber = 1;
                                        }
                                        await vm.FetchData();
                                    }
                                    else {
                                        // if(vm.moduleCode == "Appointment" && vm.subModuleCode == "Service")
                                        // {
                                            vm.ShowAlert(vm.$t('DeletedError',true,[vm.subModuleCode ]), 'failure', true, vm.$t('Alert'));
                                        // }
                                        // else
                                        // {
                                        //     vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Records')]), "failure", true, vm.$t("Alert"));
                                        // }
                                    
                                    }

                                },);
                            });
                        }
                    }
                    else {
                        vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
                    }
            //     }
            //     else
            //     {
            //         vm.ShowAlert(vm.$t('UnAuthorized'), "warning", true, "Alert");
            //         return false;
            //     }
            // })
        },
        UpdateStatus: async function (status) {          
            var vm = this;
            if (status == 'Active') {
                status = 1001;
            }
           else {
                status = 1002;
            }
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(function () {
                if (val.length > 0) {
                    val += ',';
                }
                val += $(this).val();
            });
            
            if (val.length > 0) {
                vm.Buttons[1].isdisabled=false;
                vm.Buttons[2].isdisabled=false;
                vm.Buttons[3].isdisabled=false;
                vm.confirmR(vm.$t("AreYouSureToUpdateStatus"), true, (vm.$t("update") + "  " + vm.$t("status")), function () {
                    // Send IDs as a comma-separated string
                    var postJSON = {
                        ID: val,
                        StatusId: status,
                        IsFor: "COMPANYSETUP_UPDATE",
                        Module: vm.moduleCode,
                        Submodule: vm.subModuleCode,
                    };

                    var objectPostString = JSON.stringify(postJSON);
                    CompanyDataService.CommonUpdateStatus(objectPostString).then(response => {
                        if (response.status == 200) {
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            vm.Buttons[1].isdisabled=true;
                            vm.Buttons[2].isdisabled=true;
                            vm.Buttons[3].isdisabled=true;
                            
                            vm.ShowAlert(vm.$t('UpdatedSuccess',true,[vm.subModuleCode ]), 'success', true, vm.$t('Alert'));
                            // vm.ShowAlert(vm.$t('RecordUpdatedSuccessfully'), "success", true, vm.$t("Alert"));
                            vm.FetchData();
                        }
                    });
                });
            }
        },
        getRequestParams: function (pageNumber, pageSize, sortBy, sortExp, isPaged, searchcondition) {
            let params = {};
            if (pageNumber) { params["pageNumber"] = pageNumber; }
            if (searchcondition) { params["searchcondition"] = searchcondition; }
            if (isPaged) { params["is_paged"] = isPaged; }
            if (sortBy) { params["sortBy"] = sortBy; }
            if (sortExp) { params["sortExp"] = sortExp; }
            if (pageSize) { params["pageSize"] = pageSize; }
            return params;
        },
        clickEventOfColumn: function (row, column) {
            // console.log("row",row,column.COLUMN_NAME)
            var vueObj  = this;
             if( column.DISPLAY_NAME=="Shift Name"){
                vueObj.moduleCode='Company'
             }
            if (column.COLUMN_NAME == 'title' && vueObj.moduleCode == "AnnouncementMessage") {
                this.recordId = row.id
                this.showAddPopup = true
            }

            else if (column.COLUMN_NAME == 'title' && vueObj.moduleCode == "Review") {
                this.recordId = row.review_template_id 
                this.showAddPopup = true
            }
            else if (column.COLUMN_NAME == 'title' && vueObj.subModuleCode == "Question") {              
                this.recordId = row.question_bank_id 
                this.showAddPopup = true
            }

            else if (column.COLUMN_NAME == 'title' && vueObj.moduleCode == "Skill") {
                this.recordId = row.skill_id 
                this.showAddPopup = true
            }

            else if (column.COLUMN_NAME == 'stage_name' && vueObj.moduleCode == "Stage") {                
                this.recordId = row.stage_id 
                this.showAddPopup = true
            }

            else if (column.COLUMN_NAME == vueObj.ActionColunName ) {
                
                this.recordId = row[vueObj.primaryKeyforModule]
                this.showAddPopup = true
            }                         
            
        },

        FetchData: async function (x,item,refform) {
            var vueObj = this;
            this.isLoading = true;            
            var searchCndition = vueObj.searchCondition;
            if(vueObj.moduleCode == "Company" && vueObj.subModuleCode == "Location") {   
                if(x){
                    searchCndition = 'parent_location_id ='+ x;
                } else{
                    if(searchCndition == ""){
                        searchCndition = 'parent_location_id IS NULL';
                    }
                }
            }
            var url=`CompanyId=${vueObj.CompanyId}&pageSize=${vueObj.PageSize}&pageNum=${vueObj.PageNumber}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&searchQuery=${searchCndition.trim()}&module=${vueObj.moduleCode}&submodule=${vueObj.subModuleCode}`;
            let serveUrl = vueObj.getServiceMethod(vueObj.moduleCode, vueObj.subModuleCode);
            await serveUrl(url)
                .then(function (response) {
                    if (response.data.Data != "" && response.data.Data != null) {
                        if (response.data.Data.length > 0) {
                            var dataArray = response.data.Data;
                            dataArray.forEach((item, index) => {
                                if(vueObj.moduleCode == "Company" && vueObj.subModuleCode == "AnnouncementMessage"){
                                    item.message =  vueObj.isBlank(item.message) != true ? item.message.replace(/<\/?[^>]+(>|$)/g,'') : "--";
                                }
                                if(vueObj.moduleCode == "Company" && vueObj.subModuleCode == "Department"){
                                    item.is_visibile_to_client = item.is_visibile_to_client == 1 ? 'Yes' : 'No';
                                }
                                else if(vueObj.moduleCode == "Company" && vueObj.subModuleCode == "Category"){
                                    item.category_description = vueObj.isBlank(item.category_description) != true ? item.category_description.replace(/<\/?[^>]+(>|$)/g, '') : "--";
                                    item.category_type_id = vueObj.isBlank(item.category_type_id) != true ? JSON.parse(item.category_type_id).Name : "--";
                                }
                                else if(vueObj.moduleCode == "Company" && vueObj.subModuleCode == "ContactType"){     
                                    item.type = JSON.parse(item.type).value == 0 ? "Email" :  JSON.parse(item.type).value == 1 ? "Url" : JSON.parse(item.type).value == 3 ? "Contact Number" : JSON.parse(item.type).value == 2 ? "None" : "--";
                                }                                 
                                else if(vueObj.moduleCode == "Company" && vueObj.subModuleCode == "PaymentGateway"){    
                                    item.payment_gateway_code = vueObj.isBlank(item.payment_gateway_code) != true ? item.payment_gateway_code : "--"; 
                                }   
                                else if(vueObj.moduleCode == "Company" && vueObj.subModuleCode == "ProvisionalRight"){   
                                    item.from_user_id = vueObj.isBlank(item.from_user_id) != true ? JSON.parse(item.from_user_id).Name : "--";
                                    item.to_user_id = vueObj.isBlank(item.to_user_id) != true ? JSON.parse(item.to_user_id).Name : "--";
                                }                                 
                                else if(vueObj.moduleCode == "Company" && vueObj.subModuleCode == "Stage"){                            
                                    item.phase_id = vueObj.isBlank(item.phase_id) != true ? JSON.parse(item.phase_id).Name : "--";                                                              
                                } 
                                else if(vueObj.moduleCode == "Company" && vueObj.subModuleCode == "Status"){   
                                  
                                    item.status_type_id = vueObj.isBlank(item.status_type_id) != true ? JSON.parse(item.status_type_id).Name : "--";       
                                    item.status_code =  vueObj.isBlank(item.status_code) != true ? item.status_code : "--";  
                                    Vue.set(item, "isSelected", false); // Use Vue.set to ensure reactivity
                                    if (item.company_id > 0) {
                                        Vue.set(item, "isCheckBoxDisabled", false);
                                    }
                                    else {
                                        Vue.set(item, "isCheckBoxDisabled", true);;
                                    }
                                }  
                                else if(vueObj.moduleCode == "Company" && vueObj.subModuleCode == "Greeting"){                            
                                    item.template_id = vueObj.isBlank(item.template_id) != true ? JSON.parse(item.template_id).Name : "--";               
                                }  
                                
                                else if(vueObj.moduleCode == "Company" && vueObj.subModuleCode == "UserTitle"){                            
                                    item.modified_by = vueObj.isBlank(item.modified_by) != true ? JSON.parse(item.modified_by).Name : "--";                                                              
                                }  
                                else if(vueObj.moduleCode == "Company" && vueObj.subModuleCode == "Question"){   
                                    item.modified_by = vueObj.isBlank(item.modified_by) != true ? JSON.parse(item.modified_by).Name : "--";                                                              
                                }
                                else if(vueObj.moduleCode == "Company" && vueObj.subModuleCode == "Feedback"){                            
                                    item.type_code = vueObj.isBlank(item.type_code) != true ? JSON.parse(item.type_code).Name : "--";                                                              
                                }  

                                else if(vueObj.moduleCode == "Company" && vueObj.subModuleCode == "Location"){   
                                    item.child = {
                                        onExpandChild: vueObj.onExpandChild,
                                        isShowChild: false,
                                        isShowExpandButton: true,
                                        data: []                                    
                                    }
                                    item.modified_by = vueObj.isBlank(item.modified_by) != true  ? JSON.parse(item.modified_by).Name : "--";                                                                                          
                                    item.location_country =  vueObj.isBlank(item.location_country) != true  ? JSON.parse(item.location_country).Name : "--";   
                                    item.location_state = vueObj.isBlank(item.location_state) != true ? JSON.parse(item.location_state).Name : "--";                                                           
                                } 
                                  if(vueObj.moduleCode == "Company" && vueObj.subModuleCode == "Priority"){   
                                        if(item.created_at){
                                            item.created_at = vueObj.$options.filters.formatDate(item.created_at);
                                        }
                                    }
                                if(vueObj.isBlank(item.created_by) != true){                                                    
                                    item.created_by = vueObj.isBlank(item.created_by) != true ? JSON.parse(item.created_by).Name : "--";           
                                }
                            });
                            if(refform =='EXPAND_CHILD') {
                                item.child.data = dataArray;
                            } else {
                                vueObj.ListingData = dataArray;        
                            }
                            debugger;
                            vueObj.TotalRecords = vueObj.ListingData[0].total_records;
                            vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                            vueObj.CurrentPage = vueObj.PageNumber;
                            vueObj.Buttons[1].isdisabled=true;
                            vueObj.Buttons[2].isdisabled=true;
                            vueObj.Buttons[3].isdisabled=true;
                            vueObj.noRecord = false;  
                        } 
                        else {
                            if(refform !='EXPAND_CHILD') {
                                vueObj.ListingData = [];
                                vueObj.noRecord = true;
                                vueObj.TotalRecords = 0
                            }
                        }
                    }
                    else {
                        if(refform !='EXPAND_CHILD') {
                            vueObj.noRecord = true;
                            vueObj.ListingData = [];
                            vueObj.TotalRecords = 0       
                        }                 
                    }
                    vueObj.Headers = response.data.FieldSchema; 
                    if(vueObj.subModuleCode == "Location"){
                        vueObj.Headers.push({
                            COLUMN_NAME: "AddChild",
                            DATA_TYPE: "text",
                            SORTABLE: false,
                            VISIBLE: true,
                            DISPLAY_NAME: "Add Child",
                            DISPLAY_ORDER: 10,
                        })
                    }                                                          
                    vueObj.Headers.forEach((column) => {                     
                        column.DISPLAY_NAME = vueObj.$t(column.DISPLAY_NAME);
                        if (column.COLUMN_NAME == "status_id") {                            
                                    column.settings = {
                                    isInSlot: true
                                };
                            }
                        if (vueObj.moduleCode == "Company" && column.COLUMN_NAME == "start_time" || column.COLUMN_NAME == "is_active") {                            
                                column.settings = {
                                isInSlot: true
                            };
                        }
                        if (vueObj.moduleCode == "Company" && column.COLUMN_NAME == "end_time") {                            
                                column.settings = {
                                isInSlot: true
                            };
                        }
                        if (vueObj.moduleCode == "Company" && column.COLUMN_NAME == "shift_type") {                            
                                column.settings = {
                                isInSlot: true
                            };
                        }

                        if (vueObj.moduleCode == "Company" && column.COLUMN_NAME == "break_types" || column.COLUMN_NAME == "no_of_times") {                            
                                column.settings = {
                                isInSlot: true
                            };
                        }
                        
                        if (vueObj.moduleCode == "Review" && column.COLUMN_NAME == "is_skill_enable") {                            
                                column.settings = {
                                        isInSlot: true
                            };
                        }

                        if (vueObj.moduleCode == "Review" && column.COLUMN_NAME == "is_goal_enable") {                            
                                column.settings = {
                                        isInSlot: true
                            };
                        }

                        if (vueObj.moduleCode == "Review" && column.COLUMN_NAME == "is_feedback_enable") {                            
                                column.settings = {
                                        isInSlot: true
                            };
                        }

                        if (vueObj.moduleCode == "Review" && column.COLUMN_NAME == "is_note_enable") {                            
                                column.settings = {
                                        isInSlot: true
                            };
                        }

                        if (vueObj.moduleCode == "Review" && column.COLUMN_NAME == "is_training_feedback_enabled") {                            
                                column.settings = {
                                        isInSlot: true
                            };
                        }                    

                        if (column.COLUMN_NAME == vueObj.ActionColunName) {

                            vueObj.topsearchField.map((item) => item.fieldSearchConditionName = [column.COLUMN_NAME]);
                           if(vueObj.subModuleCode != "Status"){
                                column.settings = {
                                    clickEvent: vueObj.clickEventOfColumn,
                                    formatter: vueObj.columnDataFormatter
                                };
                           }
                            
                        }
                        if (column.COLUMN_NAME == "title" && vueObj.subModuleCode == "Question") {                            
                            column.settings = {
                                clickEvent: vueObj.clickEventOfColumn,
                                formatter: vueObj.columnDataFormatter
                            }
                        }
                        if(column.COLUMN_NAME == vueObj.primaryKeyforModule){                           
                            column.VISIBLE = false; 
                        }    
                        
                        if(column.COLUMN_NAME == 'AddChild')
                        {
                            column.settings={
                                isInSlot :true
                            }
                        }

                        if (vueObj.moduleCode == "Company" && vueObj.subModuleCode == "Status" && column.COLUMN_NAME == "company_id") {                            
                            column.VISIBLE = false;
                        }
                        if (vueObj.moduleCode == "Company" && vueObj.subModuleCode == "Status" && column.COLUMN_NAME == "status_name") {                            
                            column.settings={
                                isInSlot :true
                            }
                        }
                    });  
                   
                    setTimeout(function () {
                        vueObj.ResponsiveDataTable('tablelistingdata')
                    }, 500);
                    vueObj.isLoading = false;
            });
        },
        onExpandChild: function (item) {           
            var vm = this;  
            vm.FetchData(item.company_location_id, item, 'EXPAND_CHILD');
        },
        closeModalSharePopup: function () {
            $("#modalshareview").modal('hide');
            this.showsharereportview = false;
        },
        
        getStatuslistForListing: function (StatusType, lang) {
            var vm = this;
            var url ="langCode=" + lang + "&statusType=" + StatusType + "&hasglobal=true";
                CompanyDataService.statusdropdown(url).then(function (response) {
                if (response.data.status == "Success") {
                    var json = response.data.result;
                    vm.StatusListForListing = json.data;
                }
            });
        },
        isBlank: function (str) {
            return (!str || /^\s*$/.test(str));
        },
        getServiceMethod: function(moduleCode, subModuleCode){
            
            if(moduleCode == "Company" && subModuleCode == "AnnouncementMessage"){
                return CompanyDataService.AnnouncementMessageListing;
            }
            else if(moduleCode == "Company" && subModuleCode == "Priority"){
                return CompanyDataService.GetpriorityList;
            }
            else if(moduleCode == "Company" && subModuleCode == "Question"){
                return CompanyDataService.GetQuestionList;
            }
            else if(moduleCode == "Company" && subModuleCode == "Break"){
                return CompanyDataService.BreakListing;
            }

            else if(moduleCode == "Company" && subModuleCode == "Channel"){
                return CompanyDataService.ChannelListing;
            }

            else if(moduleCode == "Company" && subModuleCode == "Category"){
                return CompanyDataService.CategoryListing;
            }

            else if(moduleCode == "Company" && subModuleCode == "ContactType"){
                return CompanyDataService.ContactTypeListing;
            }

            else if(moduleCode == "Company" && subModuleCode == "Department"){
                return CompanyDataService.DepartmentListing;
            }

            else if(moduleCode == "Company" && subModuleCode == "Designation"){
                return CompanyDataService.DesignationListing;
            }

            else if(moduleCode == "Company" && subModuleCode == "Feedback"){
                return CompanyDataService.FeedbackListing;
            }

            else if(moduleCode == "Company" && subModuleCode == "Greeting"){
                return CompanyDataService.GreetingListing;
            }

            else if(moduleCode == "Configuration" && subModuleCode == "Passport"){
                return CompanyDataService.ImmigrationListing;
            }
            
            else if(moduleCode == "Company" && subModuleCode == "IndustryTypes"){
                return CompanyDataService.IndustryTypeListing;
            }

            else if(moduleCode == "Company" && subModuleCode == "Location"){
                return CompanyDataService.LocationListing;
            }

            else if(moduleCode == "Company" && subModuleCode == "LoginAuthorization"){
                return CompanyDataService.LoginAuthorizationListing;
            }

            else if(moduleCode == "Company" && subModuleCode == "PaymentGateway"){
                return CompanyDataService.PaymentGatewayListing;
            }

            else if(moduleCode == "Company" && subModuleCode == "ProvisionalRight"){
                return CompanyDataService.ProvisionalRightListing;
            }

            else if(moduleCode == "Review" && subModuleCode == "PerformanceReview"){
                return CompanyDataService.ReviewTemplateListing;
            }

            else if(moduleCode == "Company" && subModuleCode == "Shift"){
                return CompanyDataService.ShiftListing;
            }

            else if(moduleCode == "Company" && subModuleCode == "Skill"){
                return CompanyDataService.SkillListing;
            }

            else if(moduleCode == "Company" && subModuleCode == "Stage"){
                return CompanyDataService.StageListing;
            }

            else if(moduleCode == "Company" && subModuleCode == "UserTitle"){
                return CompanyDataService.UserTitleListing;
            }

            else if(moduleCode == "Company" && subModuleCode == "Status"){
                return CompanyDataService.StatusListing;
            }

            else if(moduleCode == "Appointment" && subModuleCode == "Service"){
                return CompanyDataService.ServiceListing;
            }
            else if(moduleCode == "Company" && subModuleCode == "CheckList"){
                return CompanyDataService.CheckListListing;
            }
        }
    }
}
</script>