<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('Add') }} {{ $t('CheckList') }} </h4>
                        <button type="button" class="close" v-on:click="CloseCheckList">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>                        
                    </div>
                    <div class="modal-body">
                        <Form ref="Validateform">
                            <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" ref="AssignTagForm" 
                                v-on:OnSubmit="OnSubmit">
                            </dynamic-form>
                        </Form>
                        <div class="heading-border mb-3">
                                <h5 class="h5"><span>{{$t('Check_List_Item')}}</span>
                                    <small class="float-right">
                                        <a href="javascript:void(0)" title="" @click="AddSection" data-toggle-tooltip="tooltip" data-original-title="Add User">
                                                <em class="fa fa-plus pr-1"></em>{{$t('AddSection')}}
                                        </a>
                                    </small>
                                </h5>
                        </div>                
                        <div class="row" v-for="(item,index) in SectionData" :key="index">
                                <div class="col-lg-12">
                                    <div class="form-group">
                                        <label>{{$t('SectionTitle')}}:<span class="text-danger">* </span></label>
                                        <div class="row">
                                            <div class="col-lg-10 col-sm-12 col-xl-10 float-left">
                                                <input type="text" class="form-control" v-model="item.SectionTitle" :class="{'is-invalid':item.IsCheckSection==true}" @input="CheckSetion(item)"/>
                                                <span name="Subject" class="invalid-feedback" v-if="item.IsCheckSection">{{$t('SectionTitleRequired')}}.</span>
                                            </div>
                                            <div class="col-lg-2 col-sm-12 col-xl-2">
                                                <a href="javascript:;" @click="addItem(item,index)" class="btn btn-info mr-2">
                                                    <em class="fa fa-plus mr-2"></em>{{ $t('AddItemn') }}
                                                </a>                                            
                                                <a href="javascript:;" @click="deleteSection(index)" class="btn btn-danger" v-if="index>0">
                                                    <em class="fa fa-trash"></em> 
                                                </a>
                                            </div>
                                        </div>
                                    </div>
                                </div> 
                                <div class="col-lg-12" >
                                    <div class="col-lg-12 bg-light px-2 pt-2 border mb-3" v-for="(citem,cindex) in item.ItemData" :key="cindex">
                                        <div class="row">
                                            <div class="col-lg-3">
                                                <div class="form-group mb-0">
                                                    <label>{{ $t('Title') }}:<span class="text-danger">*</span></label>
                                                    <input type="text" class="form-control" v-model="citem.Title" :class="{'is-invalid':citem.IsItemChcek==true}" @input="CheckItemSetion(citem)"/>
                                                    <span name="Subject" class="invalid-feedback" v-if="citem.IsItemChcek">{{$t('ItemTitleRequired')}}.</span>
                                                </div>
                                            </div>
                                            <div class="col-lg-3">
                                                <div class="form-group">
                                                    <label>{{$t('AnswerType')}}:<span class="text-danger">* </span></label>
                                                    <select name="Status" class="form-control w-100" v-model="citem.AnswerType" @change="onAnswerTypeChange(citem)" :class="{'is-invalid':citem.IsitemAsnwwr==true}">
                                                        <option value="" selected="selected">{{ $t('SelectAnswerType') }}</option>
                                                        <option :value="item.value" v-for="(item,index) in AnswerTypeData" :key="index">{{item.name}} </option>                                                        
                                                    </select>
                                                    <span name="Subject" class="invalid-feedback" v-if="citem.IsitemAsnwwr">{{$t('AnswerTypeRequired')}}.</span>
                                                </div>
                                            </div>
                                            <div class="col-lg-5">
                                                <div class="form-group">
                                                    <label>{{ $t('AnswerOption') }}</label>                                                    
                                                    <div v-if="citem.AnswerType==''">--</div>
                                                    <div v-else-if="citem.AnswerType=='StarRating'">{{$t('WillShowRating')}}.</div>
                                                    <div v-else-if="citem.AnswerType=='MarkAsDone'">{{$t('YouCanMark')}}.</div>
                                                    <div v-else-if="citem.AnswerType=='TextBox'">{{$t('WillShowText')}}.</div>
                                                    <input v-else type="text" class="form-control" v-model="citem.AnswerOption" />
                                                </div>
                                            </div>
                                            <div class="col-lg-1 p-0">
                                                <div class="form-group mt-4 text-center">
                                                    <a href="javascript:;" v-if="cindex==0"  @click="addChild(item, cindex)"><em class="round-icon-small fa fa-plus bg-success float-none"></em></a>
                                                    <a href="javascript:;" @click="deleteItem(item, cindex)" v-if="cindex>0"><em class="round-icon-small fa fa-trash bg-danger float-none"></em></a>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                        </div>
                        <div class="row flex-row-reverse mt-3 mb-3">
                                <div class="col-lg-6 text-right">
                                    <a href="javascript:;" title="Save" class="btn btn-info mr-2" @click="ShowCheckListPreview">
                                        <i class="fa fa-eye pr-2"></i>{{ $t('Preview') }} </a>
                                        <a href="javascript:;" title="Save" class="btn btn-success save-btn mr-2" @click="OnSubmit">
                                <i class="fa fa-save pr-2"></i>{{ $t('Save') }}</a>
                                <a href="javascript:;" title="Cancel" class="btn btn-danger mr-2" @click="CloseCheckList">
                                    <i class="fa fa-close pr-2"></i>{{$t('Cancel')}} </a>
                                </div>
                                <div class="col-lg-6">
                                    <div class="py-2"><span class="mandatory">{{$t('MandatoryString')}}.</span></div></div>
                        </div>
                    </div> 
                </div>
            </div>
        </div>
        <CheckListPreview v-if="isCheckListPreview" :SectionData="SectionData"></CheckListPreview>
    </div>
</template>
<script>
import DataService from '../../../services/DataService'
import CheckListPreview from './CheckListPreview.vue';
export default {
    components:{CheckListPreview},
    props:{
        CheckListId:{
            type:Number,
            required:true
        }
    },
    data(){
        return{
            isLoading:false,
            buttons:[],
            FormSchema: [               
                {
                    layoutType: "triple",
                    Data: [
                    {
                            astype: "TextField",                                                
                            label: this.$t('CheckListTitle'),
                            name: "Subject",
                            value: "",               
                            placeholder: this.$t('Enter Check List Title'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "SelectField",                                                
                            label: this.$t('moduleName'),
                            name: "IssueType",
                            value: "",  
                            config: {
                                options: []
                            },                           
                            placeholder: this.$t('moduleName'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "SelectField",                                                
                            label: this.$t('Status'),
                            name: "Status",
                            value: "1001",  
                            config: {
                                options: []
                            },                           
                            placeholder: this.$t('Select Status'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "SelectField",                                                
                            label: this.$t('IndustryType'),
                            name: "IndustryType",
                            value: "",  
                            config: {
                                options: []
                            },                           
                            placeholder: this.$t('Select Industry Type'),
                            validationRules: { "required": true, max: 100 },
                        }                                                             
                    ]
                },              
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",                 
                            label: this.$t('DESCRIPTION'),
                            name: "Description",
                            value: "",  
                            validationRules: { "required": false, max: 4000 },
                            config: {
                                rows:3
                            }
                        }
                    ]
                }
            ],
            AnswerTypeData:[],
            SectionData: [{ 
                SectionTitle: '',
                IsShowItem:false,
                IsCheckSection:false,
                ItemData:[{
                    Title:'',
                    AnswerType:'',
                    AnswerOption:'',
                    IsItemChcek:false,
                    IsitemAsnwwr:false,
                }] 
            }],
            IsAddSection:false,
            isCheckListPreview:false,
            CheckListId:0
        }
    },
    created:async function(){
        var vm =this;
        await vm.GetCheckListStatus();
        await vm.GetModuleName();
        await vm.GetAnswerType();
        await vm.GetIndustryType();
        if(vm.CheckListId>0){
            vm.CheckListId=vm.CheckListId;
            vm.GetCheckListDeatils(vm.CheckListId);
        }
    },
    methods:{
        async GetIndustryType(){  
            var vm =this;          
            DataService.ChecklistGetTemplateList().then((response) => {                
                if (response.data.result.DATA.length > 0) {
                    response.data.result.DATA.forEach(element => {
                        let obj={
                            name:element.name,
                            value:element.id
                        }
                        vm.FormSchema[0].Data[3].config.options.push(obj);
                    })
                    vm.$refs.AssignTagForm.UpdateKeyValue();
                }
            })
        },
        async GetModuleName(){
            var vm =this;
            var url = `id=&moduleName=&type=PHASE_TYPE&search=&refId=`;            
            await DataService.GetDDLData(url).then((response)=>{ 
                if (response.status == 200) {
                    if (response.data.DATA != undefined || response.data.DATA != null) {
                        if (response.data.DATA.length > 0) {
                            var ModuleData=response.data.DATA;
                            ModuleData.forEach(element => {
                                let obj={
                                    name:element.name,
                                    value:element.value
                                }
                                vm.FormSchema[0].Data[1].config.options.push(obj);
                            });
                            vm.$refs.AssignTagForm.UpdateKeyValue();
                        }
                    }
                }
            })
        },
        async GetCheckListStatus(){
            var vm =this;
            var url = `id=&moduleName=&type=GLOBAL_STATUS&search=&refId=`;            
            await DataService.GetDDLData(url).then((response)=>{
                if (response.status == 200) {
                    if (response.data.DATA != undefined || response.data.DATA != null) {
                        if (response.data.DATA.length > 0) { 
                            var CheckListStatus=response.data.DATA;
                            CheckListStatus.forEach(element => {
                                let obj={
                                    name:element.name,
                                    value:element.value
                                }
                                vm.FormSchema[0].Data[2].config.options.push(obj);
                            });
                            vm.$refs.AssignTagForm.UpdateKeyValue();
                        }
                    }
                }
            })
        },
        async GetAnswerType(){
            var vm =this;
            var url = `id=&moduleName=&type=CHECK_LIST_ANSWER_TYPE&search=&refId=`;            
            await DataService.GetDDLData(url).then((response)=>{
                if (response.status == 200) {
                    if (response.data.DATA != undefined || response.data.DATA != null) {
                        if (response.data.DATA.length > 0) { 
                            vm.AnswerTypeData=response.data.DATA;
                        }
                    }
                }
            })
        },
        AddSection() {
            this.SectionData.push({
                SectionTitle: '', 
                IsCheckSection:false,               
                ItemData: [{Title:'',
                    AnswerType:'',
                    AnswerOption:'',
                    IsItemChcek:false,
                    IsitemAsnwwr:false}]
            });
        },
        showChildItem(section,index){
            var vm = this;
            section.IsShowItem=true;
        },
        addItem(section, index) {
            section.ItemData.push({
                    Title:'',
                    AnswerType:'',
                    AnswerOption:'',
                    IsItemChcek:false,
                    IsitemAsnwwr:false
                });
        },
        deleteSection(index) {
            var vm=this;
            vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Section")), function () {                
                vm.SectionData.splice(index, 1);
            })            
        },
        deleteItem(section, itemIndex) {
            var vm=this;
            vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Item")), function () {
                section.ItemData.splice(itemIndex, 1);
            })             
        },
        onAnswerTypeChange(event){
            event.IsitemAsnwwr= event.AnswerType == ''?true:false;
        },
        addChild(section, itemIndex) {
            const item = section.ItemData[itemIndex];
            section.ItemData.push({
                Title:'',
                AnswerType:'',
                AnswerOption:'',
                IsItemChcek:false,
                IsitemAsnwwr:false
            });
        },  
        GetCheckListDeatils(value){
            var ths=this;
            var ChechListId=`ChechListId=${value}`;
            DataService.GetCheckListDeatils(ChechListId).then((response)=>{
                if(response.data.result != null){
                    var CheckListData=response.data.result.CheckListData;
                    var ChecListItemData=response.data.result.CheckListDataItems;
                    if(CheckListData.length>0){
                        ths.CheckListId=CheckListData[0].checklist_id;
                        ths.FormSchema[0].Data[0].value=CheckListData[0].checklist_name;
                        ths.FormSchema[0].Data[1].value=CheckListData[0].module_id;
                        ths.FormSchema[0].Data[2].value=CheckListData[0].status_id;
                        ths.FormSchema[0].Data[3].value=CheckListData[0].industry_type_id;
                        ths.FormSchema[1].Data[0].value=CheckListData[0].description;
                    }
                    if(ChecListItemData.length>0){
                        const result = [];
                        ChecListItemData.forEach(item => {
                            let section = result.find(section => section.SectionTitle === item.section_title);
                            if (!section) {
                                section = {
                                    SectionTitle: item.section_title,
                                    IsShowItem: false,
                                    IsCheckSection:false,  
                                    ItemData: []
                                };
                                result.push(section);
                            }
                            section.ItemData.push({
                                Title: item.title,
                                AnswerType: item.answer_type,
                                AnswerOption: item.options,
                                IsItemChcek: false,
                                IsitemAsnwwr:false
                            });
                        })
                        ths.SectionData=result;
                    }
                }
            });
        }, 
        CheckSetion(event){
            event.IsCheckSection=event.SectionTitle != ''?false:true;
        },   
        CheckItemSetion(event){
            event.IsItemChcek=event.Title != ''?false:true;
        },     
        async OnSubmit(){
            var ths = this;
            ths.$refs.Validateform.validate().then(result => {
                if (result) {
                    ths.isLoading=true;
                    var isItem=false;
                    var isAnswer=false;
                    ths.SectionData.forEach(item=>{
                        if(item.SectionTitle==''){ 
                            item.IsCheckSection=true;
                            item.ItemData.forEach(itm=>{                              
                                if(itm.Title == ''){
                                    itm.IsItemChcek=true;
                                    isItem=false;
                                }
                                if(itm.AnswerType == ''){
                                    itm.IsitemAsnwwr=true;
                                    isAnswer=false;
                                }
                            })
                            ths.isLoading=false;
                            return false;
                        }else{
                            item.ItemData.forEach(itm=>{
                                if(itm.Title == ''){
                                    itm.IsItemChcek=true;
                                }else{
                                    itm.IsItemChcek=false;
                                    isItem=true;
                                }
                                if(itm.AnswerType == ''){
                                    itm.IsitemAsnwwr=true;
                                }else{
                                    itm.IsitemAsnwwr=false;
                                    isAnswer=true;
                                }
                            })
                            ths.isLoading=false;
                        }
                    })
                    if(isAnswer && isItem){
                        var obj = {
                            checklist_id:ths.CheckListId,
                            checkListTitele:ths.FormSchema[0].Data[0].value,
                            ModuleId:ths.FormSchema[0].Data[1].value,
                            Status:ths.FormSchema[0].Data[2].value,
                            Description:ths.FormSchema[1].Data[0].value,
                            IndustryTypeId:ths.FormSchema[0].Data[3].value,
                            ChechListData:ths.SectionData
                        }
                        var jsonObj=JSON.stringify(obj);
                        DataService.SaveCheckList(jsonObj).then(function (response) {                
                            ths.isLoading=false;
                            if(response.data.result.Data.length>0){ 
                                if(response.data.result.Data[0].valuess=='INSERT') {
                                    if(ths.CheckListId>0){
                                        ths.ShowAlert(ths.$t('UpdatedSuccess',[ths.$t('CheckList')]),"success", true, ths.$t("Alert"));
                                    }else{
                                        ths.ShowAlert(ths.$t('AddedSuccess',[ths.$t('CheckList')]), "success", true,  ths.$t("Alert"));
                                    } 
                                    ths.CloseCheckList();
                                    ths.$parent.FetchCheckListing();                               
                                }
                                
                            }else{
                                ths.ShowAlert(ths.$t("UnknownErrorOccur"), "failure", true, ths.$t('Alert'));
                            }
                        })
                    }
                }
            })          
        },
        CloseCheckList(){
            this.$parent.IsAddCheckList=false;
        },
        ShowCheckListPreview(){
            this.isCheckListPreview=true;
        }
    }
}
</script>