<template>
    <section>
        <loader :is-visible="isLoading"></loader>

        <tg-list :showCheckBox="true" :IsShowAction="false" :listType="['List']" ModuleName="CompanySetup"
            SubModuleCode="ManageFeedback" :PageSize="PageSize" IdentityColumn="FEEDBACK_COMPANY_OPTIONID"
            :HeaderText="$t('FeedbackOption')" :widgets="widgets" :ListData="ListData" :HeaderData="Header"
            :callbackfunction="FetchData" :LegendArray="LegendArray" :SearchFields="leftSearchFields"
            IsSearchFieldsOnTop="true" SearchPosition="top" :SortExp="SortExp" :SortBy="SortBy"
            :NorecordfoundText="$t('NorecordfoundText')" :TotalRecords="TotalRecords" :HeaderButtons="HeaderButtons"
            :RenderRowActionMethod="renderActions" :ListDataCallBackFunction="FetchData"
            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
            v-on:LeftsearchButtonClick="leftCommonSearch" v-on:SortdataButtonClick="sortdata"
            v-on:ActionButtonClick="actionButtonClick">
            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'STATUS_ID'">
                    <listing-status :isfor="'Training_Feedback'"
                        v-if="isCRMShowStausDrop(data.row.STATUS_ID, 'Training')" ref="updatestatus"
                        v-bind:dataid="data.row.FEEDBACK_COMPANY_OPTIONID" data-controller="Crm"
                        v-bind:statuslist="StatusListForListing" v-bind:item="data.STATUS_ID" data-action="index"
                        v-bind:dataselected="data.row.STATUS_ID == 1001 ? 'Active' : 'Inactive'"
                        v-bind:callbackfunction="FetchData" v-bind:isselectedvaluestring="true">
                    </listing-status>
                    <a class="inactive-disabled" v-if="!isCRMShowStausDrop(data.row.STATUS_ID, 'Training')">
                        <span v-bind:class="getStatusClass(data.row.STATUS_ID, 'STATUS_ID')">
                            {{ data.row[data.column.COLUMN_NAME] }}
                        </span>
                    </a>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'COMPANY_OPTION_TITLE'">
                    <a href="javascript:;" @click="openEditFeedback(data.row)">{{data.row.COMPANY_OPTION_TITLE }}</a>
                </template>
            </template>
        </tg-list>
        <AddFeedbackOption :OptionId="OptionId" v-if="AddItem"></AddFeedbackOption>
        <ManageFeedback :OptionId="OptionId" v-if="EditFeedback"></ManageFeedback>
    </section>
</template>
<script>
import DataService from '../../../services/DataService';
 import AddFeedbackOption from './AddFeedbackOption.vue';
  import ManageFeedback from './ManageFeedbackData.vue'
export default {
    components: { AddFeedbackOption, ManageFeedback },
    data() {
        return {
            isLoading: false,
            ListData:[],
            leftSearchFields: [{
                fieldName: 'SearchBYTrainingFeedbackTitle',
                fieldType: "text",
                fieldSearchConditionName: [''],
                paramName: "search",
                isSearch: true,
                value: "",
                listOptions: []
            }],
            listheaderbuttons: [],
            TotalRecord:5,
            PageSize: 10,
            searchCondition:'',
            searchQuery:'',
            PageNumber: 1,
            TotalRecords: 0,
            OptionId:0,
            isPaged: 1,
            TotalPages: 0,
            SortBy: '',
            SortExp: '',
            AddItem:false,
            EditFeedback: false,
            StatusListForListing: [{ STATUS: 'Active', STATUS_ID: 1001, STATUS_COLOR: "#ffffff", STATUS_BG_COLOR: "#28a745" },
            { STATUS: 'Inactive', STATUS_ID: 1002, STATUS_COLOR: "#ffffff", STATUS_BG_COLOR: "#d72435" }],
            Header: [
                {
                    "COLUMN_NAME": "COMPANY_OPTION_TITLE",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t("Title"),
                    "DISPLAY_ORDER": 1,
                    settings: {
                        isInSlot: true,
                    }
                },
                {
                    "COLUMN_NAME": "COMPANY_OPTION_DESCRIPTION",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t("DESCRIPTION"),
                    "DISPLAY_ORDER": 2,
                },
                {
                    "COLUMN_NAME": "STATUS_ID",
                    "DATA_TYPE": "text",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t("Status"),
                    "DISPLAY_ORDER": 3,
                    settings: {
                        isInSlot: true,
                    }
                }
            ],
            HeaderButtons:
            [
                {
                    title: 'Add',
                    iconClass: 'fa fa-plus',
                    callbackfunction: 'AddFeedback',
                    additionalClass: '',
                    isdisabled: false,
                    isvisible: true,
                    checkPrivilege: true,
                    id: 'AddFeedback',
                },
                {
                    id: 'ActiveMultiple',
                    title: 'Active',
                    iconClass: 'fa-check',
                    callbackfunction: 'ActiveMultipleListing',
                    additionalClass: '',
                    isdisabled: true,
                    isvisible: true,
                    checkPrivilege: true,
                    isEnabledOnSelectedRow: true
                }, {
                    id: 'InactiveMultiple',
                    title: 'Inactive',
                    iconClass: 'fa-close',
                    callbackfunction: 'InactiveMultipleListing',
                    additionalClass: '',
                    isdisabled: true,
                    isvisible: true,
                    checkPrivilege: true,
                    isEnabledOnSelectedRow: true
                },
                {
                    title: 'Delete',
                    iconClass: 'fa-trash-o',
                    callbackfunction: 'commonDelete',
                    additionalClass: '',
                    isdisabled: true,
                    isvisible: true,
                    checkPrivilege: false,
                    id: 'DeleteMultiple',
                    isEnabledOnSelectedRow: true
                }
            ],              
        }
    },
    created: async function () {
        var vm = this;
        vm.user_id = this.GetUserInfo.ID;
        vm.companyId = this.GetUserInfo.CompanyId;
        vm.FetchData();       
    },
    methods: {
        FetchData() {
            var vm = this;
            vm.isLoading = true;
            var url = `search=${vm.searchCondition}&searchQuery=${vm.searchQuery}&pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}`;
             DataService.GetFeedBackOptionListbyCompany(url).then(function (response) {                
                vm.isLoading = false;
                if (response.data) {
                    if (response.data) {
                    if (response.data.length > 0) {
                        vm.ListData = response.data;
                        vm.noRecord = false;
                        vm.TotalRecords = response.data[0].TOTALRECORDS;
                        vm.HeaderButtons[1].isdisabled = true;
                        vm.HeaderButtons[2].isdisabled = true;
                        vm.HeaderButtons[3].isdisabled = true;

                    } else {
                        vm.ListData = [];
                        vm.noRecord = true;
                        vm.TotalRecords = 0;
                    }
                } else {
                    vm.noRecord = true;
                    vm.ListData = [];
                    vm.TotalRecords = 0
                }
                }
            })
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined' && value.searchConditionJson !="") {
                let str = value.searchConditionJson;
                let result = str.split('=')[1];
                this.searchCondition = result;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        AddFeedback:function(){
            var vm=this;
            vm.AddItem=true;
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "AddFeedback":
                    this.AddFeedback();
                    break;
                case "commonDelete":
                    this.DeleteMultiple();
                    break;
                case "ActiveMultipleListing":
                    this.UpdateStatus(1001);
                    break;
                case "InactiveMultipleListing":
                    this.UpdateStatus(1002);
                    break;
                    
            }
        },
        DeleteMultiple: function (id) {
            var vm = this;
            var val = "";
            vm.isLoading = true;
            if (id == null) {
                $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(function () {
                    if (val.length > 0)
                        val += ',';
                    val += $(this).val();
                });
            }
            else {
                val = id;
            }
            if (val != null) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("TrainingFeedback")), function () {
                    var url = `ids=${val}&tablename=TALYGEN_trng_feedback_company_option&primaryKey=feedback_company_optionId`;
                    DataService.Delete(url).then(function (response) {
                        if (response.status == 200) {
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('TrainingFeedback')]), "success", true, vm.$t("Alert"));
                            vm.FetchData();
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('TrainingFeedback')]), "Success", true, vm.$t("Alert"));
                        }
                    });
                });
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
            vm.isLoading = false;
        },
        renderActions: function (row) {            
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    case 'Delete':
                        action.isVisible = true;
                        break;
                    case 'Share':
                        action.isVisible = false;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        }, 
        actionButtonClick: function (callbackfunction, item, index, event) {
            var vm = this; 
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        sortdata(sortBy) {
            var sortByName='';
            if(sortBy =='COMPANY_OPTION_TITLE'){
                sortByName='NAME';
            }else if(sortBy =='COMPANY_OPTION_DESCRIPTION'){
                sortByName ='DESCRIPTION';
            }else if(sortBy =='STATUS_ID'){
                sortByName ='STATUS'; 
            }
            else{
                sortByName=sortBy;
            }
            this.SortBy = sortByName;
            const sortExp = this.SortExp === 'ASC' ? 'DESC' : 'ASC';
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.FetchData();
        },
        UpdateStatus: async function (status) {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0) {
                    val += ',';
                }
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("AreYouSureToUpdateStatus"), true, (vm.$t("update") + "  " + vm.$t("status")), function () {
                    var postJSON = {
                        ID: val,
                        StatusId: status,
                        IsFor: "Training_Feedback",
                        Module: "Company",
                        Submodule: "ManageFeedback",
                    };                    
                    var objectPostString = JSON.stringify(postJSON);
                    DataService.CommonUpdateStatus(objectPostString).then(response => {
                        if (response.status == 200) {
                            vm.ShowAlert(vm.$t('RecordUpdatedSuccessfully', [vm.$t('Report')]), "success", true, vm.$t("Alert"));
                            vm.FetchData();
                        }
                    });
                });
            }
        },   
        openEditFeedback: function (item) {
            var vm =this;
            vm.OptionId= item.FEEDBACK_COMPANY_OPTIONID;
            vm.EditFeedback = !vm.EditFeedback;
        }, 
    }
}
</script>
