<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('Clone') }} {{ $t('CheckList') }} </h4>
                        <button type="button" class="close" v-on:click="CloseCloneCheckList">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>                        
                    </div>
                    <div class="modal-body">
                        <Form ref="Validateform">
                            <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" ref="AssignTagForm" :mandatorytext='MandatoryString'
                                v-on:OnSubmit="OnSubmit">
                            </dynamic-form>
                        </Form>                        
                    </div> 
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataService from '../../../services/DataService'
export default {
    props:{
        CheckListId:{
            type:Number,
            required:true
        }
    },
    data(){
        return{
            isLoading:false,
            MandatoryString:'Fields marked with an asterisk (*) are mandatory.',
            FormSchema: [               
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextField",                                                
                            label: this.$t('Clone Check List Name'),
                            name: "Subject",
                            value: "",               
                            placeholder: this.$t('Enter Clone Check List Name'),
                            validationRules: { "required": true, max: 100 },
                        }                                                                   
                    ]
                }                
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    isVisible:true
                },            
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.CloseCloneCheckList,
                },
            ],
            SectionData:[],
            ModuleId:0,
            Status:0,
            Description:''
        }
    },
    created:function(){
        var vm =this;
        if(vm.CheckListId>0){
            vm.CheckListId=vm.CheckListId;
            vm.GetCheckListDeatils(vm.CheckListId);
        }
    },
    methods:{
        GetCheckListDeatils(value){
            var ths=this;
            var ChechListId=`ChechListId=${value}`;
            DataService.GetCheckListDeatils(ChechListId).then((response)=>{
                if(response.data.result != null){
                    var CheckListData=response.data.result.CheckListData;
                    var ChecListItemData=response.data.result.CheckListDataItems;
                    if(CheckListData.length>0){
                        ths.ModuleId=CheckListData[0].module_id;
                        ths.Status=CheckListData[0].status_id;
                        ths.Description=CheckListData[0].description;
                    }
                    if(ChecListItemData.length>0){
                        const result = [];
                        ChecListItemData.forEach(item => {
                            let section = result.find(section => section.SectionTitle === item.section_title);
                            if (!section) {
                                section = {
                                    SectionTitle: item.section_title,
                                    IsShowItem: false,
                                    ItemData: []
                                };
                                result.push(section);
                            }
                            section.ItemData.push({
                                Title: item.title,
                                AnswerType: item.answer_type,
                                AnswerOption: item.options,
                                IsItemChcek: false
                            });
                        })
                        ths.SectionData=result;
                    }
                }
            });
        }, 
        OnSubmit(){
            var ths = this;
            ths.$refs.Validateform.validate().then(result => {
                if (result) {
                    ths.isLoading=true;
                    var obj = {
                        checklist_id:0,
                        checkListTitele:ths.FormSchema[0].Data[0].value,
                        ModuleId:ths.ModuleId,
                        Status:ths.Status,
                        Description:ths.Description,
                        ChechListData:ths.SectionData
                    }
                    var jsonObj=JSON.stringify(obj);
                    DataService.SaveCheckList(jsonObj).then(function (response) {
                        ths.isLoading=false;
                        if(response.data.result.Data.length>0){ 
                            if(response.data.result.Data[0].valuess=='INSERT') {
                                if(ths.CheckListId>0){
                                    ths.ShowAlert(ths.$t('UpdatedSuccess',[ths.$t('Clone')]),"success", true, ths.$t("Alert"));
                                }else{
                                    ths.ShowAlert(ths.$t('AddedSuccess',[ths.$t('Clone')]), "success", true,  ths.$t("Alert"));
                                } 
                                ths.CloseCloneCheckList();
                                ths.$parent.FetchCheckListing();                               
                            }
                            
                        }else{
                            ths.ShowAlert(ths.$t("UnknownErrorOccur"), "failure", true, ths.$t('Alert'));
                        }
                    })
                }
            }) 
        },
        CloseCloneCheckList(){
            this.$parent.IsCloneCheckList=false;
        },
    }
}
</script>