<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full d-flex">
                        <h4 class="modal-title">{{ $t('Manage') }} {{ $t('TrainingFeedback') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>

                    <div class="modal-body">
                        <dynamic-form ref="form" lang="en" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    props: {
        OptionId: {
            type: Number,
            required: true
        },
    },
    data() {
        return {
            isLoading: false,
            FeedbackData:[],
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('Title'),
                            name: "Title",
                            value: "",
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Status'),
                            name: "Status",
                            value: "",
                            config: {
                                options: [{ value: "1001", name: "Active" }, { value: "1002", name: "InActive" }]
                            },
                            validationRules: {
                                "required": true
                            },
                        },
                    ],
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('description'),
                            name: "Description",
                            value: "",
                            validationRules: {
                                "required": true
                            },
                        },
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Close",
                    onClick: this.Close
                }
            ],
        }
    },
    created: async function () {
        this.FetchData();
    },
    methods: {
        FetchData: async function () {
            var vm = this;
            var url = `OptionId=${vm.OptionId}`;
            await DataServices.GetFeedBackDetails(url).then(async function (response) {
                if (response.data) {
                    var FeedbackData= response.data[0];
                    vm.$refs.form.updateProperty("Title", "value", FeedbackData.Title);
                    vm.$refs.form.updateProperty("Status", "value", FeedbackData.StatusId);
                    vm.$refs.form.updateProperty("Description", "value", FeedbackData.Description);
                } 
            })
        },
        Close: function () {
            this.$parent.EditFeedback = false;
        },
        onSubmit(formfields, val) {
            var vm = this;
            var jsonobj = {
                OptionId: vm.OptionId == null ? 0 : vm.OptionId,
                Title: formfields.Title,
                Description: formfields.Description,
                StatusId: formfields.Status
            };
            var params = JSON.stringify(jsonobj);
            DataServices.SaveFeedbackData(params).then((response) => {
                if (response.data=='Success') {
                    vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("TrainingFeedback")]), "success", true, vm.$t("Alert"));
                    vm.Close();
                    vm.$parent.FetchData();
                }else if(response.data=='Duplicate'){
                    vm.ShowAlert(vm.$t("AlreadyExists", [vm.$t("TrainingFeedback")]), "failure", true, vm.$t("Alert"));
                }
                else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("TrainingFeedback")]), "failure", true, vm.$t("Alert"));
                    vm.Close();
                }
            });
        }
    }
}
</script>