<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full d-flex">
                        <h4 class="modal-title">{{ $t('Manage') }} {{ $t('TrainingFeedback') }}</h4>
                        <button type="button" class="close" @click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                    <div class="modal-body">
                        <Form ref="ManageFeedback">
                            <div class="listing">
                                <div class="table-responsive" id="divGlobalTaskList">
                                    <table id="tableGlobalTaskList"
                                        class="table table-bordered dt-responsive nowrap mt-0" style="width:100%;">
                                        <thead class="thead-bg">
                                            <tr>
                                                <th>
                                                    <label>{{ $t('Title') }}<span class="text-danger">*</span></label>
                                                </th>
                                                <th><span>{{ $t('DESCRIPTION') }}</span></th>
                                                <th class="text-center">
                                                    <span class="d-inline-block">
                                                        <a href="javascript:;"
                                                            class="round-icon-small btn-success-light float-none"
                                                            @click="addMoreRow">
                                                            <em class="fa fa-plus" aria-hidden="true"></em>
                                                        </a>
                                                    </span>
                                                </th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr v-for="(task, index) in tableData" :key="task.feedback_global_optionId"
                                                :id="'row_' + task.feedback_global_optionId"
                                                style="background-color:#cccccc;">
                                                <td>
                                                    <Field :name="'Title'" v-slot="{ errors }"
                                                        :rules="{ 'required': true }">
                                                        <input type="text" v-model="task.global_option_title"
                                                            :class="{ 'form-control tskname': true, 'is-invalid': errors[0] }"
                                                            @keypress="specialCharNotAllowed" />
                                                        <span v-if="errors[0]" :name="'Title'"
                                                            class="invalid-feedback d-block">{{
                                                                errors[0] }}</span>
                                                    </Field>
                                                </td>
                                                <td>
                                                    <textarea v-if="task.created_by > 0"
                                                        v-model="task.global_option_description" class="form-control"
                                                        rows="5" cols="50" style="min-height: 60px; width: 97%;"
                                                        @keypress="specialCharNotAllowed"></textarea>

                                                    <textarea v-else v-model="task.global_option_description"
                                                        class="form-control" rows="1" cols="15"
                                                        style="min-height: 25px; width: 97%; height: 46px;"
                                                        @keypress="specialCharNotAllowed"></textarea>

                                                </td>
                                                <td class="text-center">
                                                    <a href="javascript:;"
                                                        class="chkItems getdata round-icon-small btn-danger-light mr-2"
                                                        @click="deleteRow(task, index)">
                                                        <em class="fa fa-trash"></em>
                                                    </a>
                                                </td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>

                                <div class="col-12 mt-3 p-0">
                                    <div class="row flex-row-reverse">
                                        <div class="col-lg-6 text-right">
                                            <span class="d-inline-block">
                                                <a id="btnSave" class="btn btn-success formbtn text-white"
                                                    @click="saveData">
                                                    <em class="fa fa-save pr-2"></em>{{ $t('Save') }}
                                                </a>
                                            </span>
                                        </div>
                                        <div class="col-lg-6">
                                            <div class="py-2 text-danger">{{ $t('Mandatory') }}</div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </Form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import DataServices from "@/services/DataService";

export default {
    props: {
        OptionId: {
            type: Number,
            required: true
        },
    },
    data() {
        return {
            tableData: [],
            savedData: [],
            isLoading: false,
            FeedbackLength: 0
        };
    },
    created: async function () {
        await this.FetchData();
        await this.addMoreRow();
    },
    methods: {
        async FetchData() {
            var vm = this;
            try {
                this.isLoading = true;
                const response = await DataServices.GetUnusedCompanyFeedbackOption(`OptionId=${this.OptionId}`);
                if (response.data) {
                    vm.tableData = response.data;
                    vm.FeedbackLength = response.data.length;
                }
            } catch (error) {
                console.error("Error fetching data:", error);
            } finally {
                this.isLoading = false;
            }
        },
        Close() {
            this.$parent.AddItem = false;
        },
        addMoreRow() {
            var vm=this;
            vm.$refs.ManageFeedback.validate().then(result => {
                if (result) {
                    const newRow = {
                        feedback_global_optionId: 0,
                        global_option_title: '',
                        global_option_description: ''
                    };
                    this.tableData.push(newRow);
                }
            })
        },
        deleteRow(item, index) {
            var vm = this;
            if (item.created_by > 0) { } else {
                vm.tableData.splice(index, 1);
            }
        },
        saveData() {
            var vm = this;
            vm.$refs.ManageFeedback.validate().then(result => {
                if (result) {
                    if (vm.tableData.length <= vm.FeedbackLength) {
                        vm.ShowAlert(vm.$t("EnterAtleastOneTask"), "warning", vm.$t('Alert'));
                    } else {
                        const xmlTask = this.generateXml();
                        this.saveDataIntoDBbYxml(xmlTask);
                    }
                }
            })
        },
        generateXml() {
            var vm=this;
            let xmlTask = "<FeedbackEntries>\n";
            let filteredOptions = vm.tableData.filter(item => item.feedback_global_optionId <= 0);
            filteredOptions.forEach((task) => {
                if (task.global_option_title) {
                    xmlTask += `\n<OptionEntry>\n\t<GlobalOptionId>${task.feedback_global_optionId}</GlobalOptionId>` +
                        `\n\t<OptionTitle>${task.global_option_title}</OptionTitle>` +
                        `\n\t<Description>${task.global_option_description}</Description>` +
                        `\n</OptionEntry>\n`;
                }
            });
            xmlTask += "\n</FeedbackEntries>\n";
            return xmlTask;
        },
        saveDataIntoDBbYxml(xmlTask) {
            var vm = this;
            var jsonobj = {
                xml: xmlTask
            };
            var params = JSON.stringify(jsonobj);
            DataServices.BulkFeedbackTemplateEntry(params).then((response) => {
                if (response.data.Status == 'Success') {
                    vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("FeedbackOption")]), "success", true, vm.$t("Alert"));
                    vm.$parent.FetchData();
                    vm.Close();
                }
                else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("FeedbackOption")]), "failure", true, vm.$t("Alert"));
                }
            });
        },
        specialCharNotAllowed(e) {
            if (!/^[\d+]|[\w+]|[ ]$/.test(e.key)) {
                e.preventDefault();
            }
        },
    },
};
</script>
