<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('Preview') }} {{ $t('CheckList') }} </h4>
                        <button type="button" class="close" v-on:click="HideCheckListPreview">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>                        
                    </div>
                    <div class="modal-body" v-for="(item,index) in SectionData" :key="index"> 
                        <div class="heading-border mb-3">
                                    <h5 class="h5"><span>{{item.SectionTitle}}</span>
                                        
                                    </h5>
                        </div>
                        <div class="row">
                                <div class="col-lg-12" v-for="(itm,idx) in item.ItemData" :key="idx">
                                    <div class="form-group">
                                            {{ idx+1 }}. {{itm.Title}} 
                                            
                                            <div class="row">
                                            
                                            <div class="ml-3" v-for="(tm,dx) in itm.AnswerOption" :key="dx">
                                                <div class="col-lg-12 col-sm-12" v-if="itm.AnswerType=='CheckBox'">
                                                    <label class="form-check form-check-inline">
                                                        <div class="custom-control custom-checkbox custom-control-inline">
                                                            <input   :id="'checkbox-' + tm" type="checkbox" class="chkItems custom-control-input" :value="tm">
                                                            <label   :for="'checkbox-' + tm" class="custom-control-label pt-1 pl-1">{{tm}}</label>
                                                        </div>
                                                    </label>
                                                </div>
                                                <div class="col-lg-3"  v-if="itm.AnswerType=='Radio'">
                                                    <div class="custom-control custom-radio custom-control-inline">
                                                        <input type="radio" class="custom-control-input dynamic" :id="'radio-' + tm"  :name="'radio-' + tm" :value="tm" />
                                                        <label class="custom-control-label" :for="'radio-' + tm" >{{tm}}</label>
                                                    </div>
                                                </div>                                                
                                            </div>


                                            <div class="col-lg-12"  v-if="itm.AnswerType =='TextBox'">
                                                <input type="text" class="form-control"  /> 
                                            </div>

                                            <div class="col-lg-12"  v-if="itm.AnswerType =='StarRating'">
                                              <div class="col-lg-12">
                                                <a class="mr-2"><i class="fa fa-star text-warning"></i></a>
                                                <a class="mr-2"><i class="fa fa-star text-warning"></i></a>
                                                <a class="mr-2"><i class="fa fa-star text-warning"></i></a>
                                                <a class="mr-2"><i class="fa fa-star text-warning"></i></a>
                                                <a class="mr-2"><i class="fa fa-star text-warning"></i></a>
                                            </div>
                                            </div>


                                            <div class="col-lg-3" v-if="itm.AnswerType =='MarkAsDone'">
                                                <div class="col-lg-12 col-sm-12">
                                                <label class="form-check form-check-inline">
                                                    <div class="custom-control custom-checkbox custom-control-inline">
                                                        <input id="checkboxtm" type="checkbox" class="chkItems custom-control-input" value="">
                                                        <label for="checkboxtm" class="custom-control-label pt-1 pl-1">{{$t('MarkAsDone')}}</label>
                                                    </div>
                                                </label>
                                            </div>
                                        </div>
                                          
                                        </div>



                                    </div>
                                </div> 
                        </div>                            
                    </div> 
                    <div class="row flex-row-reverse mt-3 mb-3">
                        <div class="col-lg-6 text-right">
                            <a href="javascript:;" title="Cancel" class="btn btn-danger mr-2" @click="HideCheckListPreview">
                                <i class="fa fa-close pr-2"></i>{{$t('Close')}} </a>
                        </div>                               
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    props:{
        SectionData:{
            type:Array,
            Required:true
        }
    },
    data(){
        return{
            isLoading:false                  
        }
    },
    created: async function() {
        var vm = this;
        vm.SectionData.forEach((item) => {
            item.ItemData.forEach((ele) => {
            if (typeof ele.AnswerOption === 'string' && ele.AnswerOption.trim() !== '') {
                    ele.AnswerOption = ele.AnswerOption.split(',')
                }
            })
        })
    },
    methods:{        
        HideCheckListPreview(){
            this.$parent.isCheckListPreview=false;
        }
    }
}
</script>