<template>
    <section>
        <tg-list :IsShowAction="false" :showCheckBox="false" ModuleName="Billing" :listType="['List']"
                :RenderRowActionMethod="renderActions" SubModuleCode="Billing" IdentityColumn="client_plan_mapping_id"
                :ListData="DiscountList" :HeaderData="Headers" :callbackfunction="FetchData"
                :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :ListDataCallBackFunction="FetchData"
                :LegendArray="[]" :SortExp="sortExp" :SortBy="sortBy" :NorecordfoundText="$t('NoRecordfound')"
                v-on:LeftsearchButtonClick="[]" v-on:PagerButtonClick="pagerMethod" :isShowHeaderText="true"
                v-on:ActionButtonClick="actionButtonClick" :HeaderText="$t('RateSchedulede')"
                v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
                <template v-slot:slotdata="{ data }">
                    <template v-if="data.column.COLUMN_NAME == 'adjustment_name'">
                        <a href="javascript:;" v-on:click="Edit(data.row)"><i aria-hidden="true"></i>{{
                            data.row.adjustment_name }}
                        </a>
                    </template>
                    <template v-if="data.column.COLUMN_NAME == 'discount_from'">
                        <span>
                            {{ $options.filters.formatDateTime(data.row.discount_from) }}
                        </span>
                    </template>
                    <template v-if="data.column.COLUMN_NAME == 'discount_to'">
                        <span>
                            {{ $options.filters.formatDateTime(data.row.discount_to) }}
                        </span>
                    </template>
                    <template v-if="data.column.COLUMN_NAME == 'created_at'">
                        <span>
                            {{ $options.filters.formatDateTime(data.row.created_at) }}
                        </span>
                    </template>
                    <template v-if="data.column.COLUMN_NAME === 'status_id'">
                        <a class="inactive-disabled" :title="data.row.status_id === 1001 ? 'Active' : 'Inactive'">
                            <span :class="{
                                'btn d-inline-block text-center text-truncate': true,
                                'btn-success': data.row.status_id === 1001,
                                'btn-danger': data.row.status_id === 1002 }">
                                {{ data.row.status_id === 1001 ? 'Active' : 'Inactive' }}
                            </span>
                        </a>
                    </template>
                </template>
        </tg-list> 
    </section>
</template>

<script>
import DataService from "../../../services/DataService";
export default {
    data(){
        return{
                DiscountList:[],
                TotalRecords: 0,
                pageSize: 10,
                pageNum: 1,
                sortBy: '',
                CurrentPage: 1,
                TotalPages: 0,
                sortExp: '',
                noRecords: false,
                Headers: [
                    {
                        COLUMN_NAME: "adjustment_name",
                        DATA_TYPE: "text",
                        SORTABLE: true,
                        VISIBLE: true,
                        DISPLAY_NAME: "Name",
                        DISPLAY_ORDER: 1,
                    },
                    {
                        COLUMN_NAME: "adjustment_code",
                        DATA_TYPE: "text",
                        SORTABLE: true,
                        VISIBLE: true,
                        DISPLAY_NAME: "Code",
                        DISPLAY_ORDER: 2,
                    },
                    {
                        COLUMN_NAME: "discount",
                        DATA_TYPE: "text",
                        SORTABLE: true,
                        VISIBLE: true,
                        DISPLAY_NAME: "Discount Type",
                        DISPLAY_ORDER: 3,
                    },
                    {
                        COLUMN_NAME: "adjustment_type",
                        DATA_TYPE: "text",
                        SORTABLE: true,
                        VISIBLE: false,
                        DISPLAY_NAME: "Type",
                        DISPLAY_ORDER: 4,
                    },
                    {
                        COLUMN_NAME: "adjustment_percentage",
                        DATA_TYPE: "text",
                        SORTABLE: true,
                        VISIBLE: true,
                        DISPLAY_NAME: "Percentage",
                        DISPLAY_ORDER: 5,
                    },
                    {
                        COLUMN_NAME: "status_id",
                        DATA_TYPE: "text",
                        SORTABLE: true,
                        VISIBLE: true,
                        DISPLAY_NAME: "Status",
                        DISPLAY_ORDER: 6,
                    },
                    {
                        COLUMN_NAME: "discount_from",
                        DATA_TYPE: "text",
                        SORTABLE: true,
                        VISIBLE: true,
                        DISPLAY_NAME: "Discount From",
                        DISPLAY_ORDER: 7,
                    },
                    {
                        COLUMN_NAME: "discount_to",
                        DATA_TYPE: "text",
                        SORTABLE: true,
                        VISIBLE: true,
                        DISPLAY_NAME: "Discount To",
                        DISPLAY_ORDER: 8,
                    },
                    {
                        COLUMN_NAME: "created_by_name",
                        DATA_TYPE: "text",
                        SORTABLE: true,
                        VISIBLE: true,
                        DISPLAY_NAME: "Created By",
                        DISPLAY_ORDER: 9,
                    },
                    {
                        COLUMN_NAME: "created_at",
                        DATA_TYPE: "text",
                        SORTABLE: true,
                        VISIBLE: true,
                        DISPLAY_NAME: "Created AT",
                        DISPLAY_ORDER: 10,
                    },
                ],
                listActions: [],
            
        }
    },
    async created() {
        var vm = this;
        await vm.FetchData();
    }, 
    methods: {

        async FetchData() {
            var vueObj = this;
            await DataService.GetDiscountList().then(function (response) {
                if (response.data != "" && response.data != null) {
                    if (response.data.Result[0].length > 0) {
                        vueObj.Headers.forEach((column) => {
                            if (column.COLUMN_NAME == 'status_id') {
                                column.settings = {
                                    isInSlot: true
                                }
                            }
                            if (column.COLUMN_NAME == 'adjustment_name') {
                                column.settings = {
                                    isInSlot: true
                                }
                            }
                            if (column.COLUMN_NAME == 'discount_from') {
                                column.settings = {
                                    isInSlot: true
                                }
                            }
                            if (column.COLUMN_NAME == 'discount_to') {
                                column.settings = {
                                    isInSlot: true
                                }
                            }
                            if (column.COLUMN_NAME == 'created_at') {
                                column.settings = {
                                    isInSlot: true
                                }
                            }
                        })
                        vueObj.DiscountList=JSON.parse(response.data.Result[0])
                        // vueObj.TotalRecords = response.data.Result[0].TOTALRECORDS;
                        // vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.pageSize);
                        // vueObj.CurrentPage = vueObj.pageNum;
                        vueObj.noRecord = false;
                    } else {
                        // vueObj.noRecord = true;
                        // vueObj.TotalRecords = 0
                         vueObj.DiscountList = [];
                    }
                }
                else {
                    // vueObj.noRecord = true;
                    // vueObj.TotalRecords = 0
                    vueObj.DiscountList = [];
                }
            })
        },
        actionButtonClick(callbackfunction, item) {},
        renderActions(row) {},
        commonHeaderButtonClick(event) {},
        sortdata(sortBy) {
            this.sortBy = sortBy;
            var sortExp = 'ASC';
            if (this.sortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.sortExp = sortExp;
            this.sortBy = sortBy;
            this.sortExp = sortExp;
            this.pageNum = 1;
            this.GroupStart = this.pageNum;
            this.GroupEnd = this.pageNum + 2;
            this.FetchData();
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        getStatusClassList(status) {
            debugger
            let returnClass = '';
            switch (status) {
                case '1002':
                returnClass = 'btn btn-danger py-0 d-block font-14';
                break;
                case '1001':
                returnClass = 'btn btn-success py-0 d-block font-14';
                break;
                default:
                returnClass = '';
            }
            return returnClass;
        },
        async Edit(data) {
            this.$emit('edit-discount', data);
        }

    }
}
</script>