  </template>
  
  <script>
  import DataServices from "@/services/DataService";
  import List from "../CompanySetupListing.vue";
  import DiscountList from "../Discounts/DiscountList.vue";
import { isVisible } from "@syncfusion/ej2-base";
  export default {
    components:{List,DiscountList },
    data() {
      return {
        isLoading:false,
        isDiscountList:true,
        FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('Name'),
                            name: "Name",
                            value: "",
                            placeholder: "Enter Discount Name",
                            validationRules: {
                                "required": true
                            },
                            disabled: false,
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Code'),
                            name: "Code",
                            value: "",
                            placeholder: "Enter Discount Code",
                            validationRules: {
                                "required": false
                            },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('DiscoutType'),
                            name: "DiscoutType",
                            value: "",
                            placeholder: "",
                            config: {
                              options: [{ value: "1", name: "Fixed Amount" }, { value: "0", name: "Percentage" }],
                              onChange: this.changename,
                            },
                            validationRules: {
                                "required": true
                            },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Percentage'),
                            name: "Percentage",
                            value: "",
                            placeholder: "",
                            validationRules: {
                                "required": true
                            },
                            settings : {
                                    isInSlot: true
                                }
                        },
                        {
                            astype: "DateField",
                            label: this.$t("FromDate"),
                            name: "FromDate",
                            value: new Date(),
                            placeholder: "",
                            validationRules: {
                                required: false,
                            },
                            config: {
                                mode: "date",
                                minDate: new Date(),
                                format: "M/D/YYYY",
                                onChange: this.setTimerDate,
                                maxDate: false
                            },

                        },
                        {
                            astype: "DateField",
                            label: this.$t("FromTime"),
                            name: "FromTime",
                            value: new Date(),
                            placeholder: "",
                            validationRules: {
                                required: false,
                            },
                            config: {
                                mode: "time",
                                format: "hh:mm A"
                            },

                        },
                        {
                            astype: "DateField",
                            label: this.$t("ToDate"),
                            name: "ToDate",
                            value: new Date(),
                            placeholder: "",
                            validationRules: {
                                required: false,
                            },
                            config: {
                                mode: "date",
                                format: "M/D/YYYY",
                                maxDate: false
                            },

                        },
                        {
                            astype: "DateField",
                            label: this.$t("ToTime"),
                            name: "ToTime",
                            value: new Date(),
                            placeholder: "",
                            validationRules: {
                                required: false,
                            },
                            config: {
                                mode: "time",
                                format: "hh:mm A",
                            },

                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Status'),
                            name: "Status",
                            value: "",
                            placeholder: "Status",
                            config: {
                                options: [{ value: "1001", name: "Active" }, { value: "1002", name: "Inactive" }],
                            },
                            validationRules: {
                                "required": true
                            },
                        },
                    ]
                },
               
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save", 
                    isVisible:true,                   
                },
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Update", 
                    isVisible:false,                   
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Cancel,
                },
            ],
            adjustmentID:0
      };
    },
    methods: {
      async Cancel(){
        this.resetForm();
      },
        async submitForm() {
          var vm=this
          var form= {
          AdjustmentID: vm.adjustmentID,
          AdjOperator: 0,
          adjustment_type: 'Discounts',
          DiscountType: vm.$refs.dynamicform.getPropertyValue("DiscoutType", "value"),
          AdjName: vm.$refs.dynamicform.getPropertyValue("Name", "value"),
          AdjNameCode: vm.$refs.dynamicform.getPropertyValue("Code", "value"),
          AdjPercentage: vm.$refs.dynamicform.getPropertyValue("Percentage", "value"),
          FromDate: vm.$refs.dynamicform.getPropertyValue("FromDate", "value"),
          FromTime: vm.$options.filters.formatTime(vm.$refs.dynamicform.getPropertyValue("FromTime", "value")),
          ToDate: vm.$refs.dynamicform.getPropertyValue("ToDate", "value"),
          ToTime:vm.$options.filters.formatTime(vm.$refs.dynamicform.getPropertyValue("ToTime", "value")),
          StatusId: vm.$refs.dynamicform.getPropertyValue("Status", "value")
          }

         
            let param =JSON.stringify(form);
            await DataServices.AddUpdateDiscounts(param).then(function (response) {
               if (response.data != "" && response.data != null) {
                if (response.data.Result.length > 0) {

                  let data=JSON.parse(response.data.Result)
                  if(data[0].status==1){
                    vm.resetForm();
                    vm.ShowAlert(vm.$t("RecordSavedSuccessfully"), "success", vm.$t('Alert'));
                  }
                  else if(data[0].status==2){
                    vm.resetForm();
                    vm.ShowAlert(vm.$t("RecordUpdatedSuccessfully"), "success", vm.$t('Alert'));
                  }
                  else{
                    vm.ShowAlert(vm.$t('AlreadyExists', ['discount']), "warning", vm.$t('Alert'));
                  }
                  
                 vm.$refs.DiscountList.FetchData();
                }
                else{
                  vm.ShowAlert(vm.$t("UnknownError"), "failure", vm.$t('Alert'));
                }
               }
                
            })
        },
        populateForm(discountData) {

          debugger
          this.adjustmentID=discountData.adjustment_id
          const fromDate = new Date(discountData.from_date);
          const toDate = new Date(discountData.to_date);

          const [fromHours, fromMinutes, fromSeconds] = discountData.from_time.split(':').map(Number);
          const [toHours, toMinutes, toSeconds] = discountData.to_time.split(':').map(Number);

          const fromTime = new Date(fromDate.getFullYear(), fromDate.getMonth(), fromDate.getDate(), fromHours, fromMinutes, fromSeconds);
          const toTime = new Date(toDate.getFullYear(), toDate.getMonth(), toDate.getDate(), toHours, toMinutes, toSeconds);

          console.log("Dynamic fromTime:", fromTime);
          console.log("Dynamic toTime:", toTime);



          console.log("edit from time",fromTime)
          this.$refs.dynamicform.updateProperty("DiscoutType", "value",discountData.discount_type)
          this.$refs.dynamicform.updateProperty("Name", "value",discountData.adjustment_name)
          this.$refs.dynamicform.updateProperty("Code", "value",discountData.adjustment_code)
          this.$refs.dynamicform.updateProperty("Percentage", "value",discountData.adjustment_percentage)
          this.$refs.dynamicform.updateProperty("FromDate", "value",this.formatDateToYYYYMMDD(discountData.from_date))
          this.$refs.dynamicform.updateProperty("FromTime", "value",fromTime)
          this.$refs.dynamicform.updateProperty("ToDate", "value",this.formatDateToYYYYMMDD(discountData.to_date))
          this.$refs.dynamicform.updateProperty("ToTime", "value",toTime)
          this.$refs.dynamicform.updateProperty("Status", "value",discountData.status_id)
          this.$refs.dynamicform.UpdateKeyValue();
          this.changename()

          this.buttons[0].isVisible=false;
          this.buttons[1].isVisible=true;

          
          // this.form.AdjustmentID = discountData.adjustment_id;
          // this.form.AdjName = discountData.adjustment_name;
          // this.form.AdjNameCode = discountData.adjustment_code;
          // this.form.DiscountType = discountData.adjustment_type === 'Percentage' ? '0' : '1';
          // this.form.AdjPercentage = discountData.adjustment_percentage;
          // this.form.FromDate = this.formatDateToYYYYMMDD(discountData.from_date); 
          // this.form.FromTime = this.formatTime(discountData.from_time); 
          // this.form.ToDate = this.formatDateToYYYYMMDD(discountData.to_date); 
          // this.form.ToTime = this.formatTime(discountData.to_time);  
          // this.form.StatusId = discountData.status_id;
        },
        formatDateToYYYYMMDD(dateStr) {
          const date = new Date(dateStr);
          const year = date.getFullYear();
          const month = (date.getMonth() + 1).toString().padStart(2, '0');
          const day = date.getDate().toString().padStart(2, '0');
          return `${year}-${month}-${day}`; 
        },
        formatTime(timeStr) {
          debugger
            const timeParts = timeStr.split(":");
    
                var hours = parseInt(timeParts[0], 10);
                var minutes = parseInt(timeParts[1], 10);
                var seconds = parseInt(timeParts[2], 10);
                // Determine AM/PM
                var ampm = hours >= 12 ? 'PM' : 'AM';
                // Adjust hours to 12-hour format
                hours = hours % 12;
                hours = hours ? hours : 12; // the hour '0' should be '12'
                return { hours: hours, minutes: minutes, ampm: ampm };
        },
        resetForm(){
          var vm = this;
          vm.FormSchema.forEach((group, gi)=>{
                group.Data.forEach((field, fi) => {
                    field.value = "";
                })
            }) 
            
         // vm.$refs.dynamicform.reset;// Reset the form fields
         // vm.$refs.dynamicform.validateForm = {}; // Clear any validation messages    
          vm.$refs.dynamicform.updateProperty("FromDate", "value",new Date())
          vm.$refs.dynamicform.updateProperty("FromTime", "value",new Date())
          vm.$refs.dynamicform.updateProperty("ToDate", "value",new Date())
          vm.$refs.dynamicform.updateProperty("ToTime", "value",new Date())
          vm.$refs.dynamicform.UpdateKeyValue(); 
          vm.buttons[0].isVisible=true;
          vm.buttons[1].isVisible=false;
        },
        changename(){
          if( this.$refs.dynamicform.getPropertyValue("DiscoutType", "value")==1){
            this.$refs.dynamicform.updateProperty("Percentage", "label",'Amount')
          }
          else{
            this.$refs.dynamicform.updateProperty("Percentage", "label",'Percentage')
          }
         

        },
        setTimerDate:function(x, y, z){
          debugger
            var vm =this;
            if(y.name == "FromDate"){                           
                vm.$refs.dynamicform.updateProperty("ToDate", "config.minDate", x);
                vm.$refs.dynamicform.UpdateKeyValue();                      
            }  
        },

    }
  };
  </script>
  