<template lang="en">
<div class="my-popups">
    <loader :is-visible="isLoading"></loader>
    <div class="modal d-block">
        <div class="modal-dialog modal-dialog-centered">
            <div class="modal-content">
                <div class="modal-header theme-primary partition-full d-flex">
                    <h4 class="modal-title">{{ $t('AddTaxProductCode') }}</h4>
                    <button type="button" class="close" v-on:click="Close">
                        <em class="fa fa-times" aria-hidden="true"></em>
                    </button>
                </div>

                <div class="modal-body">
                    <dynamic-form lang="en" ref="dynamicform" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit"></dynamic-form>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import DataService from "@/services/DataService";
export default {
    props: {
        TaxData:
        {   
            type:Array,
            require:false
        }
    },
    data() {
        return {
            isLoading: false,
            SubCatData: [],
            CodeData: [],
            CountryData: [],
            StateData: [],
            DistrictData: [],
            ZipCodeData:[],
            FormSchema: [{
                    layoutType: "single",
                    Data: [
                        {
                        astype: "RadioField",
                        label: this.$t('TaxFor'),
                        name: "TaxFor",
                        value: '',
                        placeholder: "",
                        visibility: true,
                        config: {
                            options: [
                                {
                                    value: 2022,
                                    name: "Country"
                                },
                                {
                                    value: 2023,
                                    name: "State"
                                },
                                {
                                    value: 2024,
                                    name: "District"
                                },
                                {
                                    value: 2025,
                                    name: "Zip Code"
                                }
                            ],
                            showAddIcon: false,
                            onChange: this.GetRadioValue
                        },
                        validationRules: "",
                    }, 
                ],
                }, {

                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('TaxName'),
                            name: "TaxName",
                            value: "",
                            validationRules: {
                                "required": true
                            },
                        },
                        // {
                        //     astype: "SelectField",
                        //     label: this.$t('GST Code'),
                        //     name: "GSTCode",
                        //     value: "",
                        //     placeholder: "GSTCode",
                        //     validationRules: {
                        //         "required": true
                        //     },
                        //     config: {
                        //         options: [],
                        //         //onChange: this.OnChangeCountryList,
                        //         // onRemove: this.onRemoveData
                        //     },

                        // },
                        {
                            astype: "SelectField",
                            label: this.$t('COUNTRY'),
                            name: "Country",
                            value: "",
                            placeholder: "Country",
                            validationRules: {"required": true},
                            config: {
                                options: [],
                                onChange: this.OnChangeCountryList,
                                // onRemove: this.onRemoveData
                            },

                        },
                        {
                            astype: "SelectField",
                            label: this.$t('State'),
                            name: "State",
                            value: "",
                            placeholder: "State",
                            config: {
                                options: [],
                                onChange: this.OnChangeStateList,
                            },
                            visibility: false,

                        },
                        {
                            astype: "SelectField",
                            label: this.$t('District'),
                            name: "District",
                            value: "",
                            placeholder: "District",
                            config: {
                                options: [],
                                onChange: this.OnChangeDistrictList,
                            },
                            visibility: false,

                        },
                        {
                            astype: "SelectField",
                            label: this.$t('ZipCode'),
                            name: "ZipCode",
                            value: "",
                            validationRules: { "required": false },
                            config: {
                                options: []
                            },
                            visibility: false,
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('TaxType'),
                            name: "TaxType",
                            value: "",
                            placeholder: "TaxType",
                            config: {
                                options: [{
                                    name: 'percent',
                                    value: 100
                                }, {
                                    name: 'flat',
                                    value: 200
                                }],
                            },
                        },
                        {
                            astype: "NumericField",
                            label: this.$t('TaxRate'),
                            name: "TaxRate",
                            value: "",
                            placeholder: "Rate Type",
                            validationRules: {
                                "required": false
                            },
                            disabled: false,
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Status'),
                            name: "Status",
                            value: "",
                            placeholder: "Status",
                            config: {
                                options: [],
                            },
                            validationRules: {
                                "required": true
                            },
                        },
                        {

                            astype: "NumericField",
                            label: this.$t('Priority'),
                            name: "Priority",
                            value: "",
                            placeholder: "Priority",
                            validationRules: {
                                "required": false
                            },
                            disabled: false,
                        },                                                              

                    ],
                },
                {
                    layoutType: "single",
                    Data: [{
                            astype: "TextAreaField",
                            label: this.$t('DESCRIPTION'),
                            name: "Description",
                            value: "",
                            config: {
                                rows: 3
                            },
                            placeholder: this.$t(''),
                            validationRules: {
                                "required": true,
                                max: 2000,
                                // "regex": /^[a-zA-Z0-9\s]*$/
                            },
                        }

                    ]
                },
            ],
            buttons: [{
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close
                }
            ],
        }
    },
    created: async function () {
        var vm = this;        
        await vm.GetStatusList();
        // await vm.GetTaxCode();
        await vm.GetCountryList();
        (vm.FormSchema)[0].Data[0].value=2022;
        if(vm.TaxData.tax_id >0 ){            
            if(vm.TaxData.tax_for=='zipcode'){
                (vm.FormSchema)[0].Data[0].value=2025;
                (vm.FormSchema)[1].Data[2].visibility = true;
                (vm.FormSchema)[1].Data[3].visibility = true;
                (vm.FormSchema)[1].Data[4].visibility = true;
            }            
            if(vm.TaxData.tax_for=='district'){
                (vm.FormSchema)[0].Data[0].value=2024;
                (vm.FormSchema)[1].Data[2].visibility = true;
                (vm.FormSchema)[1].Data[3].visibility = true;
            }
            if(vm.TaxData.tax_for=='state'){
                (vm.FormSchema)[0].Data[0].value=2023;
                (vm.FormSchema)[1].Data[2].visibility = true;
            }     
            (vm.FormSchema)[1].Data[0].value=vm.TaxData.tax_name,
            // (vm.FormSchema)[1].Data[1].value=vm.TaxData.code_id,
            (vm.FormSchema)[1].Data[1].value=vm.TaxData.tax_country,
            vm.OnChangeCountryList(vm.TaxData.tax_country)
            (vm.FormSchema)[1].Data[2].value=vm.TaxData.tax_state,
            vm.OnChangeStateList(vm.TaxData.tax_state)
            (vm.FormSchema)[1].Data[3].value=vm.TaxData.tax_district,
            vm.OnChangeDistrictList(vm.TaxData.tax_district)
            (vm.FormSchema)[1].Data[4].value=vm.TaxData.tax_zipcode,
            (vm.FormSchema)[1].Data[5].value=(vm.TaxData.tax_type=='percent') ? 100:200,
            (vm.FormSchema)[1].Data[6].value=vm.TaxData.tax_rate,
            (vm.FormSchema)[1].Data[7].value=vm.TaxData.status_id,
            (vm.FormSchema)[1].Data[8].value=vm.TaxData.tax_rate.Priority,
            (vm.FormSchema)[2].Data[1].value=vm.TaxData.tax_desc
        }

    },
    methods: {
        Close: function () {
            this.$parent.AddItem = false;
        },
        GetRadioValue(data, event, getid) {           
            var vm = this;
            (vm.FormSchema)[1].Data[3].visibility = false;
            (vm.FormSchema)[1].Data[4].visibility = false;
            // (vm.FormSchema)[1].Data[5].visibility = false;

            if (event.value == '2023' ) {
                (vm.FormSchema)[1].Data[2].visibility = true;
            }
            if (event.value == '2024') {
                (vm.FormSchema)[1].Data[2].visibility = true;
                (vm.FormSchema)[1].Data[3].visibility = true;
            }
            if (event.value == '2025') {
                (vm.FormSchema)[1].Data[2].visibility = true;
                (vm.FormSchema)[1].Data[3].visibility = true;
                (vm.FormSchema)[1].Data[4].visibility = true;
            }
        },
        async GetStatusList() {          
            var vm = this;
            vm.isLoading = true;
            await DataService.GetStatusList().then((response) => {                
                if (JSON.parse(response.data) != null) {
                    vm.SubCatData = [];
                    vm.SubCatData = JSON.parse(response.data);
                    vm.SubCatData.forEach((itm, index) => {
                        if (itm.STATUS != 'Deleted') {
                            (vm.FormSchema)[1].Data[7].config.options.push({
                                name: itm.STATUS,
                                value: itm.STATUS_ID
                            })
                        }
                    })
                    vm.$refs.dynamicform.UpdateKeyValue();
                    vm.isLoading = false;
                }
                vm.isLoading = false;
            });
        },
        // async GetTaxCode() {
        //     var vm = this;
        //     vm.isLoading = true;
        //     await DataService.GetTaxCode().then((response) => {
        //         if (response.data != null) {
        //             vm.CodeData = [];
        //             vm.CodeData = response.data;
        //             vm.CodeData.forEach((itm, index) => {
        //                 (vm.FormSchema)[1].Data[1].config.options.push({
        //                     name: itm.codeName,
        //                     value: itm.codeId
        //                 })
        //             })
        //             vm.$refs.dynamicform.UpdateKeyValue();
        //             vm.isLoading = false;
        //         }
        //         vm.isLoading = false;
        //     });
        // },
        async GetCountryList() {
            var vm = this;
            vm.isLoading = true;
            await DataService.GetCountryList().then((response) => {
                if (response.data != null) {
                    vm.CountryData = [];
                    vm.CountryData = response.data;
                    vm.CountryData.forEach((itm, index) => {
                        (vm.FormSchema)[1].Data[1].config.options.push({
                            name: itm.countrY_FULL_NAME,
                            value: itm.id
                        })
                    })
                    vm.$refs.dynamicform.UpdateKeyValue();
                    vm.isLoading = false;
                }
                vm.isLoading = false;
            });
        },

        OnChangeCountryList: async function (x) {
            var vm = this;
            (vm.FormSchema)[1].Data[2].config.options = [];
            if ((vm.FormSchema)[1].Data[2].visibility == true) {               
                vm.isLoading = true;
                // var dropValue = x.value > 0 ? x.value : 0;
                var dropValue = typeof x === 'object' ? x.target.value : x
                var url = `CountryId=${dropValue}`;
                await DataService.GetStateList(url).then((response) => {
                    if (response.data != null) {
                        vm.StateData = [];
                        vm.StateData = response.data;
                        vm.StateData.forEach((itm, index) => {
                            (vm.FormSchema)[1].Data[2].config.options.push({
                                name: itm.stateName,
                                value: itm.stateId
                            })
                        })
                        vm.$refs.dynamicform.UpdateKeyValue();
                        vm.isLoading = false;
                    }
                    vm.isLoading = false;
                });
            }
        },
        OnChangeStateList: async function (x) {
            var vm = this;
            (vm.FormSchema)[1].Data[3].config.options = [];
            if ((vm.FormSchema)[1].Data[3].visibility == true) {             
                vm.isLoading = true;
                // var dropValue = x.value > 0 ? x.value : 0;
                var dropValue =  typeof x === 'object' ? x.target.value : x
                var url = `StateId=${dropValue}`;
                await DataService.GetDistrictListByStateId(url).then((response) => {
                    if (response.data != null) {
                        vm.DistrictData = [];
                        vm.DistrictData = response.data;
                        vm.DistrictData.forEach((itm, index) => {
                            (vm.FormSchema)[1].Data[3].config.options.push({
                                name: itm.districtName,
                                value: itm.districtId
                            })
                        })
                        vm.$refs.dynamicform.UpdateKeyValue();
                        vm.isLoading = false;
                    }
                    vm.isLoading = false;
                });
            }
        },
        OnChangeDistrictList: async function (x) {
            debugger
            var vm = this;
            (vm.FormSchema)[1].Data[4].config.options = [];
            if ((vm.FormSchema)[1].Data[4].visibility == true) {             
                vm.isLoading = true;
                // var dropValue = x.value > 0 ? x.value : 0;
                var dropValue =  typeof x === 'object' ? x.target.value : x
                // var url = `StateId=${dropValue}`;
                var url =`moduleName=CRM&type=DISTRICT_ZIPCODE&refId=${dropValue}`
                await DataService.GetDDLData(url).then((response) => {
                    debugger
                    if (response.data != null) {
                        vm.ZipCodeData = [];
                        if(response.data.DATA.length>0)
                        {
                            vm.ZipCodeData = response.data.DATA;
                            vm.ZipCodeData.forEach((itm, index) => {
                                (vm.FormSchema)[1].Data[4].config.options.push({
                                    name: itm.name,
                                    value: itm.value
                                })
                            })
                            vm.$refs.dynamicform.UpdateKeyValue();
                        }
                        vm.isLoading = false;
                    }
                    vm.isLoading = false;
                });
            }
        },
        onSubmit(formfields, val) {
            var vm = this;
            var taxFor = 'country'
            if (formfields.TaxFor == '2023') {
                taxFor = 'state';
            }
            if (formfields.TaxFor == '2024') {
                taxFor = 'district';
            }
            if (formfields.TaxFor == '2025') {
                taxFor = 'zipcode';
            }
            var jsonobj = {
                TaxName: formfields.TaxName,
                // GSTCode: formfields.GSTCode,
                TaxFor: taxFor,
                CountryId: formfields.Country,
                StateId: formfields.State,
                DistrictId: formfields.District,
                ZipCode: formfields.ZipCode,
                TaxType: formfields.TaxType == 100 ? 'percent' : 'flat',
                TaxRate: formfields.TaxRate,
                StatusId: formfields.Status,
                Priority: formfields.Priority
            };
            var params = JSON.stringify(jsonobj);
            debugger;
            DataService.SaveTaxCodes(params).then((response) => {
                if (response.data.MSG == 'SUCCESS') {
                    vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("Tax")]), "success", true, vm.$t("Alert"));
                    vm.Close();
                    vm.$emit('fetch-data');
                } else if (response.data.MSG == 'Exist') {
                    vm.ShowAlert(vm.$t('AlreadyExists', [vm.$t('Tax')]), "warning", true, vm.$t("Alert"));
                    vm.Close();
                } else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("Tax")]), "failure", true, vm.$t("Alert"));
                    vm.Close();
                }
            });
        }
    }
}
</script>
